(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .service('UpdateInfoService', UpdateInfoService);


    /** @ngInject */
    function UpdateInfoService(apilaData, authentication, BoardService) {


      var update = "";
      var issueid = "";
      var responsibleParty = "";
      var issues = "";

      /**
      * Gets an array of updateFields and formates them from proper display
      */
      function formatUpdateArray(updateInfo, updatedBy, updateDate, ipAddress) {
        _.forEach(updateInfo, function(v, k) {
          v.infoFormated = " changed " + v.field +
                            " from " + v.old + " to " + v.new;

          v.userName = updatedBy.name;
          v.ipAddress = ipAddress;

          v.userImage = updatedBy.userImage || authentication.getUserImage();


         if(v.field === "name") {
           v.field = "title";
         }

         //formating for members
         if(v.field === "members") {
           if(v.old === "") {
             v.infoFormated =  "added " + v.new + " as a Member";
           } else {
             v.infoFormated =  "removed " + v.old + " as a Member";
           }
         }

         //formating for attachemnts
         if(v.field === "attachments") {
           if(v.old === "") {
             v.infoFormated =  " added a ." + v.new  + " Attachment";
             v.tooltip = v.new;
           } else {
             v.infoFormated =  " removed a ." + v.old + " Attachment";
             v.tooltip = v.old;
           }
         }

         //formating for labels
         if(v.field === "labels") {
           if(v.old === "") {
             v.infoFormated =  "added the " + v.new + " label";
           } else {
             v.infoFormated =  "removed the " + v.old + " label";
           }
         }

         //formating for checklists
         if(v.field === "checklists") {
           if(v.old === "") {
             v.infoFormated =  " created the " + v.new + " Checklist";
           } else {
             v.infoFormated =  " removed the " + v.old + " Checklist";
           }
         }

         //formating for checklists item
         if(v.field === "checkitem") {
           if(v.old === "") {
             v.firstPart =  " added a ";
             v.checklist = "Checklist";
             v.secondPart =  "item to " + v.new;
             v.differentLayout = true;

           } else {
             v.infoFormated =  " removed a Checklist item in " + v.old;
           }
         }

         if(v.field === "checklist_name") {
           v.infoFormated =  " changed a checklist title - ";
           v.oldTip = "old";
           v.newTip = "new";
         }

         if(v.field === "checkitem_remove") {
             v.firstPart =  " removed a ";
             v.checklist = "Checklist";
             v.secondPart =  " item in " + v.old;
             v.differentLayout = true;
         }

         if(v.field === "checkitem_change") {
           v.infoFormated = " changed a checklist item - ";
           v.oldTip = "old";
           v.newTip = "new";
         }

         if(v.field === "checkitem_checked") {
              v.firstPart =  " completed a ";
              v.checklist = "Checklist";
              v.secondPart = " item in " + v.new;
              v.differentLayout = true;
         }

         if(v.field === "checkitem_unchecked") {
             v.firstPart =  " uncompleted a ";
             v.checklist = "Checklist";
             v.secondPart = " item in " + v.new;
             v.differentLayout = true;
         }

         if(v.field === "comments") {
           if(v.old === "") {
             v.infoFormated =  " created a Comment ";
             v.tooltip = v.new;
             v.multiline = true;
           }
         }

         if(v.field === "due") {
           if(v.old === "") {
             v.infoFormated = " set a Due Date ";
             v.tooltip = moment(+v.new).format('MMMM Do YYYY');
           } else {
             v.infoFormated =  " removed a Due Date ";
             v.tooltip = moment(+v.old).format('MMMM Do YYYY');
           }
         }

         if(v.field === "status") {
           v.infoFormated =  " changed the issue status to " + v.new ;
         }

         if(v.field === "downloaded") {
           v.infoFormated = "downloaded a " + v.new + " Attachment";
         }

         if(v.field === "plan-create") {
           v.infoFormated =  " created a Plan";
           v.tooltip = v.new;
           v.multiline = true;
         }

         if(v.field === "plan-todo") {
           v.infoFormated =  " crated a Plan and added it to their To-Do";
           v.tooltip = v.new;
           v.multiline = true;
         }

         if(v.field === "description" || v.field === "comment" || v.field === "plan") {
           v.twoToltips = true;
         }

         if(v.field === "description" || v.field === "title" || v.field === "resolutionTimeframe"
         || v.field === "comment" || v.field === "responsibleParty" || v.field === "plan") {
           v.infoFormated =  " changed the " + _.startCase(v.field) + " - ";
           v.oldTip = "old";
           v.newTip = "new";
         }

         if(v.field === "export") {
           v.infoFormated = " downloaded " + v.new;
         }

         v.timeDiff = timeDiff(updateDate);

        });

        return updateInfo;
      }

      function setData(issueId, updateInfo, respParty, allIssues) {
        update = updateInfo;
        issueid = issueId;
        responsibleParty = respParty;
        issues = allIssues;
      }

      function addUpdateInfo(checktooltip, fieldName, newField, oldField, finished) {

        var updateInfo = {
          updateBy : authentication.currentUser().id,
          updateDate : new Date(),
          updateField : [{
            "field": fieldName,
            "new": newField,
            "old": oldField,
            "checktooltip": checktooltip
          }]
        };

        var name = authentication.currentUser().name;

        if(!BoardService.isActiveIssue(issueid)) {

          if(responsibleParty.name === name) {
            BoardService.addActiveScore();

            BoardService.updateScore();
          } else {
            // var score = BoardService.calculateUsersScore(responsibleParty.name, issues);

            // var activityRecord = {
            //   date: new Date(),
            //   issueActivityRate: score,
            //   user: responsibleParty.id,
            //   name: responsibleParty.name
            // };

            // BoardService.saveScore(activityRecord);

          }
        }

        if(updateInfo) {
          apilaData.addUpdateInfo(issueid, updateInfo)
          .success(function(u) {

            updateInfo.updateBy = {
              'name' : authentication.currentUser().name,
              'userImage' : authentication.getUserImage()
            };
            update.push(updateInfo);

            if(finished) {
              finished(true);
            }

          })
          .error(function(err) {
            console.log(err);
            if(finished) {
              finished(false);
            }
          });
        }

      }

      function timeDiff(date) {
        var start = moment(date);
        var end = moment();

        var duration = moment.duration(end.diff(start));

        if(duration.asSeconds() < 60) {
          return Math.floor(duration.asSeconds()) + " seconds ago";
        } else if(duration.asMinutes() < 60) {
          return Math.floor(duration.asMinutes()) + " minutes ago";
        } else if(duration.asHours() < 24) {
          return Math.floor(duration.asHours()) + " hours ago";
        } else if(duration.asDays() < 31) {
          return Math.floor(duration.asDays()) + " days ago";
        } else if(duration.asMonths() < 12) {
          return Math.floor(duration.asMonths()) + " months ago";
        } else {
          return Math.floor(duration.asYears()) + " years ago";
        }

      }

      return {
        formatUpdateArray : formatUpdateArray,
        addUpdateInfo : addUpdateInfo,
        setData : setData,
        timeDiff : timeDiff
      };
    }

})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .service('MembersService', MembersService);

    /** @ngInject */
    function MembersService(apilaData, msUtils, authentication, UpdateInfoService) {

      var vm = null;

      function setViewModel(viewModel) {
        vm = viewModel;
      }

      function memberQuerySearch(query)
      {
          return query ? vm.members.filter(createFilterFor(query)) : [];
      }

      /////////////////// HELPER FUNCTIONS /////////////////////////

      function transformUpdateInfo(updateInfo) {
        updateInfo.updateBy = {
          'name' : authentication.currentUser().name,
          'userImage' : authentication.getUserImage()
        };

        return updateInfo;
      }

      function createFilterFor(query)
      {
          var lowercaseQuery = angular.lowercase(query);
          return function filterFn(item)
          {
              return angular.lowercase(item.name).indexOf(lowercaseQuery) >= 0;
          };
      }

      function filterMember(member)
      {
          if ( !vm.memberSearchText || vm.memberSearchText === '' )
          {
              return true;
          }

          return angular.lowercase(member.name).indexOf(angular.lowercase(vm.memberSearchText)) >= 0;
      }

      return {
        setViewModel : setViewModel,
        memberQuerySearch : memberQuerySearch,
        filterMember : filterMember
      };
    }

})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .service('LabelsService', LabelsService);

    /** @ngInject */
    function LabelsService(apilaData, $window, msUtils, $log, authentication, UpdateInfoService) {

      var vm = null;

      var communityId = authentication.currentUser().communityId;

      function setViewModel(viewModel) {
        vm = viewModel;
      }

      function addNewLabel()
      {

          var label = {
              id   : msUtils.guidGenerator(),
              name : vm.newLabelName,
              color: vm.newLabelColor,
              author: authentication.currentUser().name
          };

          //send data to the api
          apilaData.createLabel(communityId, label)
          .success(function(data) {
            data.id = data._id;
            vm.board.labels.push(data);

            vm.newLabelName = '';

          })
          .error(function(err) {
            $log.debug(err);
          });

      }

      function removeLabel(id)
      {

          apilaData.deleteLabel(communityId, id.name)
          .success(function(d) {

            angular.forEach(vm.board.cards, function (card)
            {
                if ( card.idLabels && card.idLabels.indexOf(vm.editLabelId) > -1 )
                {
                    card.idLabels.splice(card.idLabels.indexOf(vm.editLabelId), 1);
                }
            });

            $window.location.reload();

          })
          .error(function(response) {
            $log.debug(response);
          });

          vm.newLabelName = '';
      }

      function editLabel(label) {

        vm.labelTabIndex = 2;
        vm.editLabel = label;
        vm.editLabel.newName = label.name;

      }


      function labelQuerySearch(query)
      {
          return query ? vm.labels.filter(createFilterFor(query)) : [];
      }


      function filterLabel(label)
      {
          if ( !vm.labelSearchText || vm.labelSearchText === '' )
          {
              return true;
          }

          return angular.lowercase(label.name).indexOf(angular.lowercase(vm.labelSearchText)) >= 0;
      }

      /////////////////// HELPER FUNCTIONS /////////////////////////

      function transformUpdateInfo(updateInfo) {
        updateInfo.updateBy = {
          'name' : authentication.currentUser().name,
          'userImage' : authentication.getUserImage()
        };

        return updateInfo;
      }

      function createFilterFor(query)
      {
          var lowercaseQuery = angular.lowercase(query);
          return function filterFn(item)
          {
              return angular.lowercase(item.name).indexOf(lowercaseQuery) >= 0;
          };
      }

      return {
        addNewLabel : addNewLabel,
        removeLabel : removeLabel,
        editLabel : editLabel,
        labelQuerySearch : labelQuerySearch,
        filterLabel : filterLabel,
        setViewModel : setViewModel
      };

    }

})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .service('FileUploadService', FileUploadService);

    /** @ngInject */
    function FileUploadService(apilaData, Upload, authentication, $timeout, $mdToast, UpdateInfoService) {

      var uploadFiles = function(file, errFiles, card) {

        if(errFiles && errFiles.length > 0) {
            var error = errFiles[0].$error;

            if(error === 'maxSize') {
                 $mdToast.show(
                    $mdToast.simple()
                    .textContent("Attachment File is too Large (over 5MB)")
                    .position("top right")
                    .hideDelay(3000)
                );
            }

            return;
        }

        if(file) {
            var uploadUrl = apilaData.getApiUrl() + '/api/issues/'+ card._id + '/attachments/new';

            var fileExtensionArr = file.name.split('.');

            var fileExtension = "";

            if(fileExtensionArr && fileExtensionArr.length > 0) {
                fileExtension = fileExtensionArr.pop();
            }

            if(!fileExtension) {
            fileExtension = file.name;
            }

            var isTestCommunity = authentication.currentUser().community.testCommunity;

            if (file) {
                file.upload = Upload.upload({
                    url: uploadUrl,
                    data: {file: file, communityName: authentication.currentUser().community.name, test: isTestCommunity},
                    headers: {
                        Authorization: 'Bearer ' + authentication.getToken()
                    }
                });

                file.upload.then(function (response) {
                    $timeout(function () {
                        file.result = response.data;
                        card.attachments.push(response.data);

                        UpdateInfoService.addUpdateInfo('', "attachments", fileExtension, "");
                    });
                }, function (err) {

                    var errMsg = angular.isString(err) ? err : err.data.message;

                    errMsg = errMsg || "Sorry we are unable to upload the specified file";

                    $mdToast.show(
                        $mdToast.simple()
                        .textContent(errMsg)
                        .position("top right")
                        .hideDelay(3000)
                    );

                }, function (evt) {
                    file.progress = Math.min(100, parseInt(100.0 *
                                            evt.loaded / evt.total));
                });
            }
        }

      };

      return {
        uploadFiles : uploadFiles
      };
    }


})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .service('ChecklistsService', ChecklistsService);

    /** @ngInject */
    function ChecklistsService(apilaData, $log, msUtils, authentication, UpdateInfoService, $mdToast, UndoService) {

      var vm = null;

      function setViewModel(viewModel) {
        vm = viewModel;
        //UpdateInfoService.setData(vm.card._id, vm.card.updateInfo);
      }

      function updateCheckedCount(list, checkedItem, type)
      {
          var checkItems = list.checkItems;
          var checkedItems = 0;
          var allCheckedItems = 0;
          var allCheckItems = 0;

          if(!checkedItem) {
            return;
          }

          angular.forEach(checkItems, function (checkItem)
          {
              if ( checkItem.checked )
              {
                  checkedItems++;
              }
          });

          list.checkItemsChecked = checkedItems;

          angular.forEach(vm.card.checklists, function (item)
          {
              allCheckItems += item.checkItems.length;
              allCheckedItems += item.checkItemsChecked;
          });

          vm.card.checkItems = allCheckItems;
          vm.card.checkItemsChecked = allCheckedItems;

          apilaData.updateCheckList(vm.card._id, list._id, list)
          .success(function(d) {

            if(type !== 'add') {
              if(checkedItem.checked) {
                UpdateInfoService.addUpdateInfo(checkedItem.name, "checkitem_checked", list.checklistName, "");
              } else {
                UpdateInfoService.addUpdateInfo(checkedItem.name, "checkitem_unchecked", list.checklistName, "");
              }
            }

          })
          .error(function(response) {
            $log.debug(response);
          });
      }


      function updateCheckListName(list, oldLists) {


        var currList = _.find(oldLists, {_id: list._id});

        apilaData.updateCheckList(vm.card._id, list._id, list)
        .success(function(d) {
          UpdateInfoService.addUpdateInfo('', "checklist_name", list.checklistName, currList.checklistName);
        })
        .error(function(response) {
          $log.debug(response);
        });
      }

      function restoreCheckItem() {
        var checklistOfItem = UndoService.getItem('checkitem');

        if(checklistOfItem) {

          var cardIndex = _.findIndex(vm.card.checklists, {_id: checklistOfItem._id});

          vm.card.checklists[cardIndex] = checklistOfItem;

          apilaData.updateCheckList(vm.card._id, checklistOfItem._id, checklistOfItem)
          .success(function(d) {

          });
        }
      }

      function addCheckItem(text, checkList)
      {
          if ( !text || text === '' ) { return; }

          var newCheckItem = {
              'name'   : text,
              'checked': false
          };

          checkList.checkItems.push(newCheckItem);

          apilaData.updateCheckList(vm.card._id, checkList._id, checkList)
          .success(function(d) {

              UpdateInfoService.addUpdateInfo(newCheckItem.name, 'checkitem', checkList.checklistName, "");
              updateCheckedCount(checkList, newCheckItem, 'add');
              text = "";
              vm.oldData.checklists = d;

          })
          .error(function() {
            $log.debug("Error while updateing checklist");
          });

      }


      function removeChecklist(item)
      {
        //send remove request to the api
        apilaData.deleteCheckList(vm.card._id, item._id)
        .success(function(d) {
          vm.card.checklists.splice(vm.card.checklists.indexOf(item), 1);

          UpdateInfoService.addUpdateInfo('', "checklists", "", item.checklistName);

          angular.forEach(vm.card.checklists, function (list)
          {
              updateCheckedCount(list);
          });

          UndoService.setItem(item, 'checklist');
          UndoService.showActionToast('checklist', function(){ createCheckList('restore'); });
        })
        .error(function(d){
          $log.debug("Error while removing checklist");
        });

      }

      function createCheckList(type)
      {

          var data = {
              id               : msUtils.guidGenerator(),
              name             : vm.newCheckListTitle,
              checklistName    : vm.newCheckListTitle,
              checkItemsChecked: 0,
              checkItems       : [],
              author           : authentication.currentUser().id
          };

          vm.newCheckListTitle = '';

          if(type === 'restore') {
            data = UndoService.getItem('checklist');
          }

          apilaData.addCheckList(vm.card._id, data)
          .success(function(d) {
              vm.card.checklists.push(d);

              if(type !== 'restore') {
                UpdateInfoService.addUpdateInfo('', 'checklists', data.name, "");
              }
              vm.newCheckListTitle = "Checklist";
          })
          .error(function(err) {
            $log.debug(err);
          });
      }

      function removeCheckItem(checklist, i) {
        var checkItemName = checklist.checkItems[i].name;
        var copyChecklist = angular.copy(checklist);

        checklist.checkItems.splice(i, 1);

        apilaData.updateCheckList(vm.card._id, checklist._id, checklist)
        .success(function(d) {

          UpdateInfoService.addUpdateInfo(checkItemName, 'checkitem_remove', "" , checklist.checklistName);

          UndoService.setItem(copyChecklist, 'checkitem');
          UndoService.showActionToast('checkitem', function(){ restoreCheckItem(); });

        })
        .error(function() {
          $log.debug("Error while updateing checklist");
        });



      }

      function updateCheckItem(checklist, checkitemId, text) {
        checklist.checkItems[checkitemId] = text;

        var oldChecklist = _.find(vm.oldData.checklists, {_id: checklist._id});
        var oldName = "";

        if(oldChecklist) {
          oldName = oldChecklist.checkItems[checkitemId].name;
        }

        UpdateInfoService.addUpdateInfo('', 'checkitem_change', checklist.checkItems[checkitemId].name , oldName , function(resp) {
          if(resp) {
            apilaData.updateCheckList(vm.card._id, checklist._id, checklist);
          }
        });

      }

      //////////////////////// HELPER FUNCTIONS ///////////////////////////////
      function transformUpdateInfo(updateInfo) {
        updateInfo.updateBy = {
          'name' : authentication.currentUser().name,
          'userImage' : authentication.getUserImage()
        };

        return updateInfo;
      }

      return {
        setViewModel : setViewModel,
        updateCheckedCount : updateCheckedCount,
        updateCheckListName : updateCheckListName,
        addCheckItem : addCheckItem,
        removeChecklist : removeChecklist,
        createCheckList : createCheckList,
        removeCheckItem : removeCheckItem,
        updateCheckItem : updateCheckItem
      };

    }

})();

