(function ()
{
    'use strict';

    angular
        .module('app.todo')
        .controller('TaskDialogController', TaskDialogController);

    /** @ngInject */
    function TaskDialogController($mdDialog, $log, users, Task, Tasks, event, $mdToast, msNavigationService, authentication, apilaData, ToDoUtilsService, ToDoService)
    {
        var vm = this;

        // Data
        vm.title = 'Edit Task';
        vm.task = angular.copy(Task);
        vm.newTask = false;
        vm.tasks = Tasks;

        vm.canSubmit = false;

        var weekMap = ToDoService.weekMap;

        var userid = authentication.currentUser().id;

        vm.dayMap = ToDoService.dayMap;
        vm.todoid = authentication.currentUser().todoid;

        vm.isBossOrDirector = false;

        var community = authentication.currentUser().community;

        if(community.boss._id === userid || _.find(community.directors, {_id: userid})) {
          vm.isBossOrDirector = true;
        }

        if ( !vm.task ) {
            vm.title = 'New Task';
            vm.newTask = true;
            vm.form = {
              "text" : "",
              "occurrence" : 1,
              "activeDays" : [],
              "activeMonths" : [false, false, false, false, false, false, false, false, false, false, false, false ],
              "activeWeeks": [false, false, false, false, false],
              hourStart: 0,
              hourEnd: 23
            };

            // Monday - Friday selected by default
            for(var i = 0; i < 5; ++i) {
              vm.form.activeDays[i] = true;
            }

        } else {
          vm.newTask = false;
          vm.form = vm.task;

          vm.form.startTime = castToDate(vm.form.startTime);
          vm.form.endTime = castToDate(vm.form.endTime);

          vm.form.weekStartTime = castToDate(vm.form.weekStartTime);
          vm.form.weekEndTime = castToDate(vm.form.weekEndTime);

          if(vm.task.selectedWeekDay) {
            var selectedWeekArr = vm.task.selectedWeekDay.toString().split('');
            vm.selectedWeekDayText = weekMap[selectedWeekArr[0]] + " " + vm.dayMap[selectedWeekArr[1]];
          }

        }

        loadUsersList(authentication.currentUser().community._id);

        // Methods
        vm.addNewTask = addNewTask;
        vm.updateTask = updateTask;
        vm.closeDialog = closeDialog;
        vm.cancelDialog = cancelDialog;
        vm.selectEveryMonth = selectEveryMonth;
        vm.selectEveryWeek = selectEveryWeek;
        vm.selectWeekDay = selectWeekDay;
        vm.getMatches = getMatches;

        function addNewTask()
        {
            vm.form.communityId = authentication.currentUser().community._id;

            if(vm.selectedUser) {
              vm.form.responsibleParty = vm.selectedUser.value;

              var todoid = _.find(vm.usersList, {value: vm.selectedUser.value}).todoid;
              vm.form.responsibleTodoid = todoid;
            }
            
            apilaData.addTask(vm.todoid, vm.form)
            .success(function(task) {
              errorMessages(task, "created");
              vm.tasks.push(task);
              closeDialog();
            })
            .error(function(response) {
              $log.debug(response);
            });

        }

        function updateTask() {

          if(vm.selectedUser) {
            vm.form.responsibleParty = vm.selectedUser.value;
          }

          //responsibleParty changed 
          if(vm.selectedUser.value !== vm.oldResponsibleParty.value) {
            vm.form.oldResponsibleTodoid = vm.oldResponsibleParty.todoid;
          }

          var todoid = _.find(vm.usersList, {value: vm.selectedUser.value}).todoid;
          vm.form.responsibleTodoid = todoid;

          vm.form.creatorsTodoid = _.find(vm.usersList, {value: vm.form.submitBy._id}).todoid;

          apilaData.updateTask(vm.todoid, vm.task._id,  vm.form)
          .success(function(task) {

            errorMessages(task, "updated");

            // Update the correct tasks with new values
            for(var i = 0; i < vm.tasks.length; ++i) {
              if(vm.tasks[i]._id === vm.task._id) {
                vm.tasks[i].text = vm.form.text;
                vm.tasks[i].occurrence = vm.form.occurrence;
                break;
              }
            }

            closeDialog();
          })
          .error(function(response) {
            $log.debug(response);
          });

        }

        function selectEveryMonth() {
            vm.form.activeMonths = _.map(vm.form.activeMonths, function(elem) {
              return vm.form.everyMonth;
            });
        }

        function selectEveryWeek() {
            vm.form.activeWeeks = _.map(vm.form.activeWeeks, function(elem) {
              return vm.form.everyWeek;
            });
        }

        function getMatches(text) {
          if(text === null) {
            return;
          }

          var textLower = text.toLowerCase();

          var ret = vm.usersList.filter(function (d) {
              if(d.display != null) {
                return d.display.toLowerCase().indexOf(textLower) > -1;
              }
          });

          return ret;
        }

        function errorMessages(task, action) {
          if(!isInActiveCycle(task)) {
            showToast("The task is " + action +" but it's not currently active");
          } else {
            apilaData.activeTasksCount(vm.todoid).then(function(response) {
              msNavigationService.saveItem('fuse.to-do', {
                badge: {
                  content: response.data,
                  color: '#FF6F00'
                }
              });
            });
          }
        }

        function showToast(msg) {
          $mdToast.show(
            $mdToast.simple()
            .textContent(msg)
            .position("top right")
            .hideDelay(3000)
          );
        }


        function selectWeekDay(week, day) {

          vm.form.selectedWeekDay = week.toString() + day.toString();

          vm.selectedWeekDayText = weekMap[week] + " " + vm.dayMap[day];
        }

        function isInActiveCycle(task) {

          var currTime = moment();
          var currHour = currTime.hour();
          var currDay = currTime.isoWeekday();
          var currWeek = weekOfMonth(currTime);
          var currMonth = currTime.month();

          var result = false;

          if(task.occurrence === 0) {
            result = (currHour >= task.hourStart && currHour <= task.hourEnd);
          } else if(task.occurrence === 1) {
            result = task.activeDays[currDay - 1];
          } else if(task.occurrence === 2) {
            result = task.activeWeeks[currWeek - 1];
          } else if(task.occurrence === 3) {
            result = task.activeMonths[currMonth];
          }

          //sometimes an array will be like activeDays will have less items
          if(result === undefined) {
            result = false;
          }

          return result;

        }

        function weekOfMonth(m) {
          return m.week() - moment(m).startOf('month').week() + 1;
        }

        function cancelDialog() {
          $mdDialog.hide(true);
        }

        function closeDialog()
        {
            $mdDialog.hide(false);
        }

        function castToDate(date) {
            if(date) {
              return moment(date).toDate()
            }
        }

        function loadUsersList(communityId) {
          apilaData.usersInCommunity(communityId)
            .success(function(usersList) {
              vm.usersList = usersList.map(function(user) {

                setResponsibleParty(user);

                return {
                  value: user._id,
                  display: user.name,
                  todoid: user.todoid
                };
              });

            })
            .error(function(usersList) {
              $log.debug("Error retriving the list of users");
            });
        }

        function setResponsibleParty(user) {

          var isUser = vm.newTask ? userid === user._id: user._id === vm.task.responsibleParty._id;

            if (isUser) {
                vm.selectedUser = {
                  value: user._id,
                  display: user.name,
                  todoid: user.todoid
                };

                vm.oldResponsibleParty = angular.copy(vm.selectedUser);
            }
        }

        setTimeout(function() {
          vm.slider = {
              options: {
                floor: 0,
                ceil: 23,
                noSwitching: true,
                showTicks: true,
                translate: function(value) {
                  return ToDoUtilsService.convertToPm(value);
                }
              }
            };
        }, 350);
    }
})();

