(function() {
  'use strict';

  angular.module('app.residents')
    .controller('UpdateController', UpdateController);

  /** @ngInject */ 
  function UpdateController($mdDialog, $mdConstant, Upload, $timeout, $window, UndoService, $mdToast, errorCheck, SliderMapping, $log, category,
                            currResident, residentDisplay, apilaData, authentication, ResidentUpdateInfoService) {

    var vm = this;

    //DATA
    vm.form = currResident;

    vm.form.firstName = residentDisplay.firstName;
    vm.form.contact = {};
    vm.selctedTab = "";

    vm.form.communicatedWithResident = false;
    vm.form.communicatedWithPrimaryContact = false;
    vm.form.communicatedWithTrustedPerson = false;

    vm.category = category;

    vm.replaceNumberValue = SliderMapping.replaceNumberValue;

    var community = authentication.currentUser().community;
    var userid = authentication.currentUser().id;

    if(community.boss._id === userid) {
      vm.isBoss = true;
    }

    if(_.find(community.directors, {"_id" : userid})) {
      vm.isDirector = true;
    }

    var requiredArray = ["firstName", "lastName", "buildingStatus"];

    vm.status = createMultiSelect(["Alert", "Friendly", "Disoriented",
      "Withdrawn", "Lonely", "Happy", "Confused", "Uncooperative",
      "At times angry", "Sad", "Emotional outbursts", "Feel like a burden"
    ]);

    vm.isNumber = function(n) {
      if(n === "" || n === undefined) {return true;}
      return !isNaN(parseFloat(n)) && isFinite(n);
    };

    vm.shopping = createMultiSelect(["Family", "Self", "Friend"]);

    vm.painManagedBy = createMultiSelect(['Medication', 'Hot pack', 'Cold pack', 'Positioning', 'Topicals']);

    setSelectedStatuses(currResident.psychosocialStatus);
    setSelectedShopping(currResident.shopping);
    setSelectedPainManagedBy(currResident.painManagedBy);

    vm.foodAllergies = currResident.foodAllergies;
    vm.medicationAllergies = currResident.medicationAllergies;

    vm.foodLikes = currResident.foodLikes;
    vm.foodDislikes = currResident.foodDislikes;
    vm.bathingDays = currResident.bathingDays;

    //needed unchanged values to compare for updateField
    vm.copyResident = angular.copy(currResident);

    vm.seperators = [$mdConstant.KEY_CODE.ENTER, $mdConstant.KEY_CODE.COMMA];

    vm.autocompleteOptions = {
      componentRestrictions: {
        country: 'us'
      }
    };

    vm.error = {};

    vm.hasPrimaryContact = (_.find(currResident.residentContacts, {"primaryContact": true}) !== undefined);
    vm.hasTrustedPerson = (_.find(currResident.residentContacts, {"trustedPerson": true}) !== undefined);

    vm.community = authentication.currentUser().community;

    vm.roomList = _.flatten(_.map(vm.community.roomStyle, "rooms"));

    vm.form.birthDate = vm.form.birthDate ? new Date(currResident.birthDate) : undefined;
    vm.form.admissionDate = vm.form.admissionDate ? new Date(currResident.admissionDate) : undefined;

    if(vm.form.movedFrom) {
      vm.form.locationInfo = vm.form.movedFrom.name;
    }

    //Functions
    vm.closeDialog = closeDialog;
    vm.updateResident = updateResident;
    vm.updateChip = updateChip;
    vm.uploadFiles = uploadFiles;
    vm.addContact = addContact;
    vm.getMatches = getMatches;
    vm.deleteResident = deleteResident;
    vm.saveCrop = saveCrop;

    vm.uploadImage = uploadImage;

    vm.formatFileName = formatFileName;

    function closeDialog(data) {
      $mdDialog.hide(data);
    }

    function uploadImage(file, errFiles) {
      var uploadUrl = apilaData.getApiUrl() + '/api/residents/'+ currResident._id + '/image';

          if(errFiles && errFiles.length > 0) {
              var error = errFiles[0].$error;

              if(error === 'maxSize') {
                  $mdToast.show(
                      $mdToast.simple()
                      .textContent("Avatar File is too Large (over 5MB)")
                      .position("top right")
                      .hideDelay(3000)
                  );
              }

              return;
          }

          if (file) {

            if(file.type === 'image/jpg' || file.type === 'image/jpeg' || file.type === 'image/png') {

              file.upload = Upload.upload({
                  url: uploadUrl,
                  data: {file: file, community: authentication.currentUser().community.name },
                  headers: {
                      Authorization: 'Bearer ' + authentication.getToken()
                  }
              });

              file.upload.then(function (response) {
                  $timeout(function () {
                      file.result = response.data;

                      currResident.residentImage = response.data;

                      $mdToast.show(
                        $mdToast.simple()
                          .textContent("Resident image updated!")
                          .position("top right")
                          .hideDelay(1000)
                      );

                  });

              }, function(err) {
                    var errMsg = angular.isString(err) ? err : err.data.message;

                    errMsg = errMsg || "Sorry we are unable to upload the specified file";

                    $mdToast.show(
                        $mdToast.simple()
                        .textContent(errMsg)
                        .position("top right")
                        .hideDelay(3000)
                    );
              });
            } else {
              $mdToast.show(
                    $mdToast.simple()
                    .textContent("You can upload only a .jpg or a png image")
                    .position("top right")
                    .hideDelay(3000));
            }
        }
    }

    function saveCrop() {

       var uploadUrl = apilaData.getApiUrl() + '/api/residents/'+ currResident._id + '/image';

       if(!currResident.residentImage) {
         return;
       }

       var urlSplit = currResident.residentImage.split('/');
       var name = urlSplit[urlSplit.length - 1];

       Upload.upload({
            url: uploadUrl,
            data: {
                file: Upload.dataUrltoBlob(vm.croppedDataUrl, "cropped-" + name),
                community: authentication.currentUser().community.name
            },
            headers: {
                    Authorization: 'Bearer ' + authentication.getToken()
                }
        }).then(function(response) {

            currResident.residentImage = response.data.url;

            $mdToast.show(
                $mdToast.simple()
                .textContent("Resident image cropped")
                .position("top right")
                .hideDelay(2000)
            );

            closeDialog();
        });
    }

    //before sending to server set all the fields and format them correctly
    function formatData() {

      vm.form.modifiedBy = authentication.currentUser().id;
      vm.form.modifiedDate = new Date();

      addToStatusArray();
      addToShoppingArray();

      addToPainManagedBy();

      var changedFields =
        ResidentUpdateInfoService.checkChangedFields(vm.copyResident, vm.form);

      if(!vm.form.movedFrom) {
        vm.form.movedFrom = {};
      }

      if (vm.form.locationInfo.geometry) {
        vm.form.movedFrom.name = vm.form.locationInfo.formatted_address;
        vm.form.movedFrom.latitude = vm.form.locationInfo.geometry.location.lat();
        vm.form.movedFrom.longitude = vm.form.locationInfo.geometry.location.lng();
      }

      if (changedFields.length > 0) {
        vm.form.updateField = changedFields;
      }

    }


    function updateResident(type) {

      if(errorCheck.requiredFields(vm.form, vm.error, requiredArray)) {
        return;
      }

      if(!vm.form.room && type === 'administrative') {
        vm.form.room = vm.searchText;
      }

      formatData();

      vm.form.newfoodAllergies = vm.foodAllergies;
      vm.form.newmedicationAllergies = vm.medicationAllergies;

      vm.form.newfoodLikes = vm.foodLikes;
      vm.form.newfoodDislikes = vm.foodDislikes;

      //important to set updateInfo when adding/removing chips because they will generate updateInfo
      vm.form.updateInfo = currResident.updateInfo;

      userObjectsToIds();

      apilaData.updateResident(currResident._id, vm.form)
        .success(function(resident) {

          currResident.updateInfo = resident.updateInfo;
          currResident.carePoints = resident.carePoints;

          pushNewValues();
          resetFields();

          console.log(currResident);

          closeDialog(currResident);
        })
        .error(function(response) {
          $log.debug(response);
        });

    }


    function updateChip(list, type, chip, operation) {

      var data = {
        "operation": operation,
        "list": list,
        "type": type,
        "selectedItem": chip,
        "updateBy": authentication.currentUser().id
      };


      apilaData.updateListItem(vm.copyResident._id, data)
        .success(function(response) {
          currResident = response;
          $log.debug(currResident);
        })
        .error(function(response) {
          $log.debug(response);
        });
    }

    function addContact() {

      vm.form.contact.submitBy = authentication.currentUser().id;

      apilaData.addContact(currResident._id, vm.form.contact)
      .success(function(response) {
        currResident.residentContacts = response;
        closeDialog();
      })
      .error(function(response) {
        $log.debug(response);
      });
    }

    function uploadFiles(file, errFiles) {

      var uploadUrl = apilaData.getApiUrl() + '/api/residents/' + currResident._id + '/upload';

      if (file) {

        vm.fileName = file.name;

        file.upload = Upload.upload({
          url: uploadUrl,
          data: {
            file: file
          },
          headers: {
            Authorization: 'Bearer ' + authentication.getToken()
          }
        });

        file.upload.then(function(response) {
          $timeout(function() {

            file.result = response.data;
            vm.fileUploaded = true;
          });

        });
      }
    }

    function getMatches(text) {

      if(text === null) {
        return vm.roomList;
      }

      var textLower = text.toLowerCase();

      var ret = vm.roomList.filter(function (d) {
        if(d) {
          return d.toLowerCase().indexOf(textLower) > -1;
        }
      });

        return ret;
    }

    function deleteResident() {

      UndoService.setItem(currResident, 'resident');

      apilaData.deleteResident(currResident._id)
      .success(function() {

        UndoService.showActionToast('delete', function() {
          var resident = UndoService.getItem('resident');

          apilaData.resurrectResident(resident);

          $window.location.reload();
        });

        vm.closeDialog('deleted');


      })
      .error(function(err) {
        console.log(err);
      })
    }


    //////////////////////// HELPER FUNCTIONS //////////////////////////////////

    // Goes through updateInfo each UpdateBy field and switches an user object with an userid
    function userObjectsToIds() {
      _.map(vm.form.updateInfo, function(updateField) {
        if(updateField.updateBy) {
          updateField.updateBy = updateField.updateBy._id;
        }

        return updateField;
      });
    }

    function addToShoppingArray() {
      vm.form.newShoppingStatus = [];

      for (var i = 0; i < vm.shopping.length; ++i) {
        if (vm.shopping[i].active == true) {
          vm.form.newShoppingStatus.push(vm.shopping[i].title);
        }
      }
    }

    function setSelectedPainManagedBy(arr) {

      if(!arr) {
        return;
      }

      for (var i = 0; i < vm.painManagedBy.length; ++i) {
        var checkedElem = _.find(vm.painManagedBy, function(d) {
          if (d.title == arr[i]) {
            return d;
          }
        });

        if (checkedElem != undefined) {
          checkedElem.active = true;
        }
      }
    }

    // sets which psychosocialStatus is checked
    function setSelectedShopping(arr) {

      if(!arr) {
        return;
      }

      for (var i = 0; i < vm.shopping.length; ++i) {
        var checkedElem = _.find(vm.shopping, function(d) {
          if (d.title == arr[i]) {
            return d;
          }
        });

        if (checkedElem != undefined) {
          checkedElem.active = true;
        }
      }
    }

    function addToStatusArray() {
      vm.form.newpsychosocialStatus = [];

      for (var i = 0; i < vm.status.length; ++i) {
        if (vm.status[i].active === true) {
          vm.form.newpsychosocialStatus.push(vm.status[i].title);
        }

      }
    }


    function addToPainManagedBy() {
      vm.form.newPainManagedBy = [];

      for (var i = 0; i < vm.painManagedBy.length; ++i) {
        if (vm.painManagedBy[i].active === true) {
          vm.form.newPainManagedBy.push(vm.painManagedBy[i].title);
        }

      }
    }

    // sets which psychosocialStatus is checked
    function setSelectedStatuses(arr) {

      if(!arr) {
        return;
      }

      for (var i = 0; i < vm.status.length; ++i) {
        var checkedElem = _.find(vm.status, function(d) {
          if (d.title == arr[i]) {
            return d;
          }
        });

        if (checkedElem != undefined) {
          checkedElem.active = true;
        }
      }
    }

    function formatFileName(file) {
      var fileName = file.split("/").pop();
      return unescape(fileName);
    }

    var resetFields = function() {
      vm.form.newrespiration = "";
      vm.form.newvitalsPain = "";
      vm.form.newpulse = "";
      vm.form.newweight = "";
      vm.form.newoxygenSaturation = "";
      vm.form.newbloodPressureDiastolic = "";
      vm.form.newbloodPressureSystolic = "";
      vm.form.newtemperature = "";
      vm.form.newinternationalNormalizedRatio = "";

      vm.form.newmedicationAllergies = "";
      vm.form.newfoodAllergies = "";
      vm.form.newfoodLikes = "";
      vm.form.newfoodDislikes = "";
    };

    var pushNewValues = function() {
      addToArray(vm.form.respiration, vm.form.newrespiration);
      addToArray(vm.form.vitalsPain, vm.form.newvitalsPain);
      addToArray(vm.form.pulse, vm.form.newpulse);
      addToArray(vm.form.weight, vm.form.newweight);
      addToArray(vm.form.oxygenSaturation, vm.form.newoxygenSaturation);
      addToArray(vm.form.bloodPressureDiastolic, vm.form.newbloodPressureDiastolic);
      addToArray(vm.form.bloodPressureSystolic, vm.form.newbloodPressureSystolic);
      addToArray(vm.form.temperature, vm.form.newtemperature);
      addToArray(vm.form.internationalNormalizedRatio, vm.form.newinternationalNormalizedRatio);

      vm.form.foodAllergies = vm.form.newfoodAllergies;

      vm.form.medicationAllergies = vm.form.newmedicationAllergies;

      //addToArray(vm.form.psychosocialStatus, vm.form.newpsychosocialStatus);
      vm.form.psychosocialStatus = vm.form.newpsychosocialStatus;
      vm.form.shopping = vm.form.newShoppingStatus;
      vm.form.painManagedBy = vm.form.newPainManagedBy;

      vm.form.foodLikes = vm.form.newfoodLikes;
      vm.form.foodDislikes = vm.form.newfoodDislikes;
    };

    //when pushing to array make sure we aren't adding invalid data
    function addToArray(arr, value) {

      if (value != undefined) {
        if (value != "") {
          arr.push(value);
          return true;
        }
      } else {
        return false;
      }

    }


    ///////////////////////////////////////////////////////////////////////////


    // Builds up proper array of object for multi select fields like psyho status
    function createMultiSelect(titleArray) {

      var selectArr = [];

      for (var i = 0; i < titleArray.length; ++i) {
        selectArr.push({
          active: false,
          title: titleArray[i]
        });
      }

      return selectArr;
    }


  }

})();

(function ()
{
    'use strict';

    angular
        .module('app.residents')
        .controller('ComposeDialogController', ComposeDialogController);

    /** @ngInject */
    function ComposeDialogController($mdDialog, $log, apilaData, resList, allList, originalList, authentication, errorCheck)
    {
        var vm = this;

        // Methods
        vm.closeDialog = closeDialog;
        vm.addResident = addResident;

        // Data
        vm.residentList = resList;
        vm.form = {};
        vm.form.room = "";
        vm.form.movedFrom = "Denver, CO, USA";
        vm.error = {};

        var requiredArray = ["firstName", "lastName", "buildingStatus"];

        var userid = authentication.currentUser().id;

        vm.autocompleteOptions = {
          componentRestrictions: { country: 'us' }
        };

        apilaData.userCommunity(userid)
         .success(function(d) {
           vm.community = d;
        });

        function closeDialog()
        {
            $mdDialog.hide();
        }

        function addResident()
        {
          vm.form.community =  vm.community;

          if(errorCheck.requiredFields(vm.form, vm.error, requiredArray)) {
            return;
          }

          setLocationData();

          apilaData.addResident(vm.form)
          .success(function(resident) {

              resList.push(resident);
              allList.push(resident);
              originalList.push(resident);
              
              $mdDialog.hide();
          })
          .error(function(response) {
            $log.debug(response);
          });
        }


        function setLocationData() {
          vm.form.movedFrom = {};

          if(vm.form.locationInfo) {
            vm.form.movedFrom.name = vm.form.locationInfo.formatted_address;
            vm.form.movedFrom.latitude = vm.form.locationInfo.geometry.location.lat();
            vm.form.movedFrom.longitude = vm.form.locationInfo.geometry.location.lng();
          }

        }
    }
})();

