(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .controller('msSbAddCardController', msSbAddCardController)
        .directive('msSbAddCard', msSbAddCardDirective);

    /** @ngInject */
    function msSbAddCardController($scope, $timeout, $log, BoardService, $mdDialog, $document, msUtils)
    {
        var vm = this;

        vm.newCardName = '';
        vm.listId = $scope.msListId;
        vm.board = BoardService.data.data;
        vm.cards = vm.board.cards;
        vm.list = vm.board.lists.getById(vm.listId);

        // Methods
        vm.addNewCard = addNewCard;

        /////

        vm.addIssue = function(ev) {

                $log.debug(vm.list.name);
          $mdDialog.show({
              controller         : 'CreateIssueController',
              controllerAs       : 'vm',
              locals             : {
                  board: vm.board,
                  name: vm.list.name
              },
              templateUrl        : 'app/main/issues/dialogs/createIssue/createIssue.html',
              parent             : angular.element($document.body),
              targetEvent        : ev,
              clickOutsideToClose: true
          });
        }

        /**
         * Add New Card
         */
        function addNewCard()
        {
            if ( vm.newCardName === '' )
            {
                return;
            }

            var newCardId = msUtils.guidGenerator();

            vm.cards.push({
                id               : newCardId,
                name             : vm.newCardName,
                description      : '',
                idAttachmentCover: '',
                idMembers        : [],
                idLabels         : [],
                attachments      : [],
                subscribed       : false,
                checklists       : [],
                checkItems       : 0,
                checkItemsChecked: 0,
                comments         : [],
                activities       : [],
                due              : null
            });

            vm.list.idCards.push(newCardId);

            $timeout(function ()
            {
                $scope.scrollListContentBottom();
            });

            vm.newCardName = '';
        }
    }

    /** @ngInject */
    function msSbAddCardDirective($document, $window, $timeout)
    {
        return {
            restrict   : 'E',
            controller : 'msSbAddCardController as vm',
            templateUrl: 'app/main/issues/directives/ms-sb-add-card/ms-sb-add-card.html',
            scope      : {
                msListId: '='
            },
            link       : function (scope, iElement)
            {
                scope.formActive = false;
                scope.toggleForm = toggleForm;
                scope.scrollListContentBottom = scrollListContentBottom;

                var buttonEl = iElement.find('.ms-sb-add-card-button'),
                    formEl = iElement.find('.ms-sb-add-card-form'),
                    listCards = iElement.parent().prev().find('.list-cards');

                /**
                 * Click Event
                 */
                buttonEl.on('click', toggleForm);

                /**
                 * Toggle Form
                 */
                function toggleForm()
                {
                    scope.$evalAsync(function ()
                    {
                        scope.formActive = !scope.formActive;

                        if ( scope.formActive )
                        {
                            $timeout(function ()
                            {
                                formEl.find('input').focus();

                                scrollListContentBottom();
                            });

                            $document.on('click', outSideClick);
                        }
                        else
                        {
                            PerfectScrollbar.update(listCards[0]);
                            $document.off('click', outSideClick);
                        }

                        $timeout(function ()
                        {
                            // IE list-content max-height hack
                            if ( angular.element('html').hasClass('explorer') )
                            {
                                angular.element($window).trigger('resize');
                            }
                        });

                    });
                }

                /**
                 * Scroll List to the Bottom
                 */
                function scrollListContentBottom()
                {
                    listCards[0].scrollTop = listCards[0].scrollHeight;
                }

                /**
                 * Click Outside Event Handler
                 * @param event
                 */
                var outSideClick = function (event)
                {
                    var isChild = formEl.has(event.target).length > 0;
                    var isSelf = formEl[0] === event.target;
                    var isInside = isChild || isSelf;

                    if ( !isInside )
                    {
                        toggleForm();
                    }
                };
            }
        };
    }
})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .controller('WordCloudController', WordCloudController);

    /** @ngInject */
    function WordCloudController($mdDialog, issue, wordCloud, $timeout) {

      var vm = this;

      vm.closeDialog = closeDialog;

      // give it a bit of time to load
      $timeout(function () {
          wordCloud.drawWordCloud(createWordArray());
      }, 100);


      // gets all the comments and issue description and converts them to word array
      function createWordArray() {

          var wordCloudText = "";
          for (var i = 0; i < issue.comments.length; ++i) {
              wordCloudText += " " + issue.comments[i].commentText;
          }

          // adding description to the word cloud
          wordCloudText += " " + issue.description;

          // adding cheklist items name to te word cloud
          angular.forEach(issue.checklists, function(v, k) {
            angular.forEach(v.checkItems, function(item, key) {
              wordCloudText += " " + item.name;
            });
          });

          return wordCloudText.split(" ");

      }

      function closeDialog()
      {
        $mdDialog.hide();
      }

    }

})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .controller('StatsController', StatsController);

    /** @ngInject */
    function StatsController($mdDialog, BoardService, board, stats, labelStats, apilaData, authentication) {

        var vm = this;

        vm.colorMapping = {
            'red': '#F44336',
            'pink': '#E91E63',
            'purple': '#9C27B0',
            'deep-purple': '#673AB7',
            'indigo': '#3F51B5',
            'blue': '#2196F3',
            'light-blue': '#03A9F4',
            'cyan': '#00BCD4',
            'teal': '#009688',
            'green': '#4CAF50',
            'light-green': '#8BC34A',
            'lime': '#CDDC39',
            'yellow': '#FFEB3B',
            'amber': '#FFC107',
            'orange': '#FF9800',
            'deep-orange': '#FF5722',
            'brown': '#795548',
            'grey': '#9E9E9E',
            'blue-grey': '#607D8B'
        };

        vm.activityRate = BoardService.getScore();

        vm.closeDialog = closeDialog;

        vm.username = authentication.currentUser().name;
        vm.userid = authentication.currentUser().id;
        vm.communityid = authentication.currentUser().community._id;

        vm.labels = _.map(board.data.labels, "name");
        vm.labelColors = _.map(board.data.labels, function(label) {
            return {
                "strokeColor": vm.colorMapping[label.color],
                "pointColor": vm.colorMapping[label.color]
            };
        });

        vm.tableSettings = {
            order     : [1, 'desc'],
            paging    : false,
            searching : false
        };

        createActivityTable();

        getActivityRates();

        //////////////////// PRE LOAD DATA  ////////////////////////

        apilaData.userActivityRankings(vm.communityid)
        .success(function(rankings) {

            for(var rank in rankings) {

                var sorted = _.sortBy(rankings[rank], "date");

                if(rankings.hasOwnProperty(rank)) {

                    var curr = rankings[rank];
                    var score = sorted[sorted.length -1].issueActivityRate;

                    // if the current user has some issues gone inactive, record the new results
                    if(rank === vm.username && vm.activityRate.toFixed(2) !== score.toFixed(2)) {
                        score = BoardService.updateScore(function() {
                            getActivityRates();
                        });
                    }

                }
            }

        });

        apilaData.getLabelStats(vm.communityid)
        .success(function(resp) {

            var data = [];
            var timeFrame = [];
            var longest = 0;

            //for every label in the community
            vm.labels.forEach(function(labelName) {

                //all the labels for a particular labelname
                var filteredByLabel = _.filter(resp, {name: labelName});

                if(filteredByLabel) {

                    //grab all the score values
                    var dataList  =_.map(filteredByLabel, 'score');

                    data.push(dataList);

                    if(longest < dataList.length) {
                        longest = dataList.length;
                    }

                    if(timeFrame === [] || longest > timeFrame.length) {
                        
                        timeFrame = _.map(filteredByLabel, function(d) {
                            return moment(d.date).format("MMM Do YY");
                        });
                    }
                }
            
            });

            populateLabelData(timeFrame, data, longest);

        });

        function getActivityRates() {
            apilaData.getActivityRates(vm.communityid, vm.userid)
              .success(function(resp) {

                if(resp.length === 0) {
                    BoardService.updateScore();
                }

                populateChartData(resp);

            });
        }

        function populateChartData(resp) {

            var dataValues = _.map(resp, "issueActivityRate");
            var timeFrame = _.map(resp, function(d) {
                return moment(d.date).format("MMM Do YY");
            });

            vm.chartData = {
                labels: timeFrame,
                series: [vm.username + " Activity rate"],
                data: [dataValues]
            };
        }

        function populateLabelData(timeFrame, data, longest) {

            var filledData = [];

            data.forEach(function(list) {

                if(list.length < longest) {
                    list = addZeroes(list, longest - list.length);
                }

                filledData.push(list);

            });

            vm.labelData = {
                labels: timeFrame,
                series: vm.labels,
                data: filledData,
                options: {
                     datasetFill: false
                },
                colours: vm.labelColors
            };

        }

        function createActivityTable() {

            var table = BoardService.calculateTable();

            vm.tableScore = [];

            for(var name in table) {

                var numIssues = table[name].allIssues;
                var numActiveIssues = table[name].activeIssues;
                var score = 0;

                if(table.hasOwnProperty(name)) {
                    score = calculateScore(numIssues, numActiveIssues);
                }

                vm.tableScore.push({
                    name: name,
                    score: score
                });
            }
        }

        function calculateScore(numIssues, numActiveIssues) {
            var score = (numActiveIssues / numIssues) * 100;

            if(numActiveIssues === 0 || numIssues === 0) {
                score = 0;
            }

            return score;
        }

        // Adds n zeroes at the begging of an existing array
        function addZeroes(arr, num) {
            var emptyArr = _.fill(new Array(num), 0);

            return emptyArr.concat(arr);
        }

        function closeDialog() {
            $mdDialog.hide();
        }

    }

})();
(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .controller('CreateIssueController', CreateIssueController);

    /** @ngInject */
    function CreateIssueController($mdDialog, $log, apilaData, board, BoardService, name, errorCheck, authentication, msNavigationService) {

      var vm = this;

      // Data
      var username = authentication.currentUser().name;
      vm.userid = authentication.currentUser().id;

      vm.residentList = [];
      vm.selectedUser = {};
      vm.form = {};
      vm.error = {};

      var requiredArray = ["title", "resolutionTimeframe", "description"];

      //Functions
      vm.closeDialog = closeDialog;
      vm.addIssue = addIssue;
      vm.getMatches = getMatches;

      apilaData.userCommunity(vm.userid)
      .success(function(d) {
        vm.myCommunity = d;

         // load of lists of residents for autocomplete selection
        apilaData.usersInCommunity(vm.myCommunity._id)
          .success(function(usersList) {
            vm.residentList = usersList.map(function(elem) {

              if (elem.name === name) {
                vm.selectedItem = {
                  value: elem._id,
                  display: elem.name
                };
              }

              return {
                value: elem._id,
                display: elem.name
              };
            });

          })
          .error(function(usersList) {
            $log.debug("Error retriving the list of residents");
          });
      });

      function closeDialog()
      {
          $mdDialog.hide();
      }

      function getMatches(text) {
        if(text === null) {
          return;
        }

        var textLower = text.toLowerCase();

        var ret = vm.residentList.filter(function (d) {
            if(d.display != null) {
              return d.display.toLowerCase().indexOf(textLower) > -1;
            }
        });

        return ret;
      }

       function checkForm() {
         var error = false;

         if(errorCheck.requiredFields(vm.form, vm.error, requiredArray)) {
           error = true;
         }

         if(!vm.selectedItem) {
           vm.error.selectedItem = true;
           error = true;
         } else {
           vm.error.selectedItem = false;
         }

         return error;
       }

      function addIssue() {

        if(checkForm()) { return; }

        //if the responsibleParty isn't curr. user notify the other user
        if(vm.userid !== vm.selectedItem.value) {
          vm.form.notifyUser = true;
        }

        vm.form.responsibleParty = vm.selectedItem.value;
        vm.form.community = vm.myCommunity;

        apilaData.addIssue(vm.form)
            .success(function(issue) {

              //format issue for frontend
              issue.id = issue._id;
              issue.name = issue.title;

              addIssueToList(issue);

              updateIssueCount();

              if(issue.responsibleParty.name === username) {
                BoardService.updateScore();
              }

              closeDialog();
            })
            .error(function(issue) {
                $log.debug("Error while adding issue");
            });
      }

      /////////////////////// HELPER FUNCTIONS //////////////////////////

      function updateIssueCount() {
        apilaData.openIssuesCount(vm.userid, vm.myCommunity._id)
          .success(function(count) {

            if(count <= 0) {
                msNavigationService.saveItem('fuse.issues', { badge: null });
                return;
            }

            msNavigationService.saveItem('fuse.issues', {
              badge: {
                content:  count,
                color  : '#F44336'
              }
            });
          })
          .error(function(response) {
            $log.debug(response);
          });
      }

      function addIssueToList(issue) {

        //wierd stuff with board.data
        if(board.data === undefined) {
          board.data = board;
        }

        // add to general list of cards
        board.data.cards.push(issue);

        // find the right list to add a new issue to
        var rightList = _.find(board.data.lists, {"name" : issue.responsibleParty.name});

        rightList.idCards.push(issue.id);

      }

    }

})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .controller('IssueCardDialogController', IssueCardDialogController);

    /** @ngInject */
    function IssueCardDialogController($document, $mdDialog, fuseTheming, $scope, $window, UndoService, $timeout, exportIssueDetail, LabelsService, ChecklistsService, $mdToast,
      fuseGenerator, msUtils, BoardService, cardId, apilaData, authentication, msNavigationService, $log, FileUploadService, UpdateInfoService, MembersService, Utils)
    {
        var vm = this;

        // Data
        LabelsService.setViewModel(vm);
        ChecklistsService.setViewModel(vm);
        MembersService.setViewModel(vm);

        vm.board = BoardService.data.data;
        vm.card = vm.board.cards.getById(cardId);

        vm.card.currdue = vm.card.due;

        vm.newLabelColor = 'red';
        vm.UpdateInfoService = UpdateInfoService;

        vm.Utils = Utils;

        vm.issueTimeAgo = moment(vm.card.submitDate).fromNow();

        vm.labels = vm.board.labels;

        var unchangedDueDate = angular.copy(vm.card.due);
        var oldData = angular.copy(vm.card);

        vm.oldData = oldData;

        vm.newCheckListTitle = "Checklist";

        vm.username = authentication.currentUser().name;
        var userid = authentication.currentUser().id;
        vm.userid = userid;
        vm.communityId = authentication.currentUser().communityId;

        vm.now = new Date();

        // Functions
        vm.palettes = fuseTheming.getRegisteredPalettes();

        //remove colors we had problem with
        delete vm.palettes['fuse-pale-blue'];
        delete vm.palettes['fuse-blue'];

        vm.rgba = fuseGenerator.rgba;
        vm.toggleInArray = msUtils.toggleInArray;
        vm.exists = msUtils.exists;

        vm.existsMembers = function(item, arr) {
          return _.find(arr, {_id: item._id}) ? true : false;
        };

        vm.toggleMember = function(item, arr) {
          var exists = _.find(arr, {_id: item._id}) ? true : false;

          if(exists) {
            arr.splice(arr.indexOf(item), 1);
          } else {
            arr.push(item);
          }
        };

        vm.removeAttachment = removeAttachment;

        // Labels
        vm.labelQuerySearch = LabelsService.labelQuerySearch;
        vm.filterLabel = LabelsService.filterLabel;
        vm.addNewLabel = LabelsService.addNewLabel;
        vm.removeLabel = LabelsService.removeLabel;
        vm.removeLabelFromCard = removeLabelFromCard;
        vm.editLabel = LabelsService.editLabel;
        vm.addLabelToCard = addLabelToCard;
        vm.isLabelInCard = isLabelInCard;

        // Members
        vm.memberQuerySearch = MembersService.memberQuerySearch;
        vm.filterMember = MembersService.filterMember;
        vm.addMembers = addMembers;

        // Checklist
        vm.updateCheckedCount = ChecklistsService.updateCheckedCount;
        vm.addCheckItem = ChecklistsService.addCheckItem;
        vm.updateCheckListName = ChecklistsService.updateCheckListName;
        vm.removeChecklist = ChecklistsService.removeChecklist;
        vm.createCheckList = ChecklistsService.createCheckList;
        vm.removeCheckItem = ChecklistsService.removeCheckItem;
        vm.updateCheckItem = ChecklistsService.updateCheckItem;

        vm.addNewComment = addNewComment;
        vm.updateComment = updateComment;
        vm.updateIssue = updateIssue;

        vm.updatePlan = updatePlan;

        vm.editPermissions = editPermissions;
        // Main field update
        vm.removeDueDate = removeDueDate;
        vm.memberUpdate = memberUpdate;
        vm.updateLabel = updateLabel;
        vm.addMemberAutoComplete = addMemberAutoComplete;

        vm.changeConfidential = function(confidentialChange) {
          console.log("Change confidential");

          vm.card.confidential = confidentialChange;

          apilaData.updateConfidential(vm.card._id, {confidential: confidentialChange})
          .success(function(resp) {
            console.log(resp);
          })
          .error(function(err) {
            $log.debug(err);
          });

        };

        //Other
        vm.wordCloud = wordCloud;
        vm.closeDialog = closeDialog;
        vm.getCardList = getCardList;
        vm.removeCard = removeCard;
        vm.openImage = openImage;
        vm.changeStatus = changeStatus;
        vm.exportIssue = exportIssue;
        vm.addFinalPlan = addFinalPlan;

        vm.downloadedInfo = downloadedInfo;

        vm.formatUpdateArray = UpdateInfoService.formatUpdateArray;

        vm.getMatches = getMatches;

        vm.changeResponsibleParty = changeResponsibleParty;

        vm.uploadFiles = function(file, invalidFiles, card) {
          FileUploadService.uploadFiles(file, invalidFiles, card);
        };

        // load of lists of residents for autocomplete selection
        apilaData.usersInCommunity(vm.communityId)
          .success(function(usersList) {
            vm.residentList = usersList.map(function(elem) {

              if (elem.name === name) {
                vm.selectedItem = {
                  value: elem._id,
                  display: elem.name
                };

              }

              return {
                value: elem._id,
                display: elem.name
              };
            });

          })
          .error(function(usersList) {
            $log.debug("Error retriving the list of residents");
          });

        function getMatches(text) {
          if(text === null) {
            return;
          }

          var textLower = text.toLowerCase();

          var ret = vm.residentList.filter(function (d) {
              if(d.display != null) {
                return d.display.toLowerCase().indexOf(textLower) > -1;
              }
          });

          return ret;
        }

        // Load comments
        apilaData.issueCommentsList(vm.card._id)
        .success(function(response) {
          vm.card.comments = response;
        })
        .error(function(response) {
          $log.debug(response);
        });

        // Load updateInfo
        apilaData.issueUpdateInfo(vm.card._id)
        .success(function(response) {
          vm.card.updateInfo = response;
          UpdateInfoService.setData(vm.card._id, vm.card.updateInfo, vm.card.responsibleParty, vm.board.cards);
        })
        .error(function(response) {
          $log.debug(response);
        });

        apilaData.userCommunity(userid)
        .success(function(d) {

          vm.myCommunity = d;

          setUserRole();

          loadIssueCount(d._id);

          //load member list
          apilaData.usersInCommunity(d._id)
          .success(function(response) {
            vm.members = response;
          })
          .error(function(response) {
            $log.debug(response);
          });
        });

        function restoreAttachment() {
          var deletedAttachment = UndoService.getItem('attachment');

          vm.card.attachments.push(deletedAttachment);

          apilaData.restoreAttachment(vm.card._id, deletedAttachment)
          .success(function(resp) {

          });
        }


        function removeAttachment(item)
        {
            if ( vm.card.idAttachmentCover === item.id ){
                vm.card.idAttachmentCover = '';
            }

            var fileExtension = item.name.split('.').pop();

            if(!fileExtension) {
              fileExtension = item.name;
            }

            apilaData.deleteAttachment(vm.card._id, item._id, vm.card)
            .success(function(d) {
              vm.card.attachments.splice(vm.card.attachments.indexOf(item), 1);
              UpdateInfoService.addUpdateInfo('', 'attachments', "" , fileExtension);

              UndoService.setItem(item, 'attachment');
              UndoService.showActionToast('attachment', function() { restoreAttachment(); })
            })
            .error(function(d) {
              $log.debug(d);
            });
        }

        //////////////////////////// LABEL /////////////////////////////////


        function addLabelToCard(id) {

          if(id) {
            var label = _.find(vm.board.labels, {_id: id});

            if(!isLabelInCard(label.name)) {

              apilaData.addLabelToCard(vm.card._id, label)
              .success(function(resp) {

                  if(!isLabelInCard(label.name)) {
                    vm.card.labels.push(label);
                  } 

                 UpdateInfoService.addUpdateInfo('', 'labels', label.name, "");
              })
              .error(function(err) {
                $log.debug(err);
              });

              updateLabelStats(label.name, "add");

            } else {
               vm.removeLabelFromCard(label.name);

               UpdateInfoService.addUpdateInfo('', 'labels', "", label.name);

               updateLabelStats(label.name, "remove");
            }
           
          }

        }

        console.log(vm.palettes);

        function removeLabelFromCard(labelname, fromchip) {

          updateLabelStats(labelname, 'remove');

          apilaData.removeLabelFromCard(vm.card._id, labelname)
          .success(function(resp) {

            if(!fromchip) {
              var index = _.findIndex(vm.card.labels, {name: labelname});

              if(index != null) {
                vm.card.labels.splice(index, 1);
              }
            }

          })
          .error(function(err) {
            $log.debug(err);
          });

        }

        ////////////////////////////// MEMBERS ////////////////////////////

        function addMembers(item, array) {

            var exists = vm.existsMembers(item, array);

            vm.toggleMember(item, array);

             var data = {
              memberEmail: item.email,
              memberName: item.name,
              memberId: item._id,
              issue: vm.card
            };

            apilaData.sendMemberNotification(vm.card._id, data)
            .then(function(resp) {
              console.log(resp);
            })
            .catch(function(err) {
              console.log(err);
            });

            if(exists) {
              UpdateInfoService.addUpdateInfo('', 'members', "", item.name, function(resp) {
                if(resp) {
                  updateIssue();
                }
              });
            } else {
              UpdateInfoService.addUpdateInfo('', 'members', item.name, "", function(resp) {
                if(resp) {
                  updateIssue();
                }
              });
            }

        }

        function addMemberAutoComplete(selectedMember) {
          if(selectedMember !== null) {
            vm.card.addedMember = selectedMember;

            var data = {
              memberEmail: selectedMember.email,
              memberName: selectedMember.name,
              memberId: selectedMember._id,
              issue: vm.card
            };

            apilaData.sendMemberNotification(vm.card._id, data)
            .then(function(resp) {
              console.log(resp);
            })
            .catch(function(err) {
              console.log(err);
            });

            vm.card.idMembers.push(selectedMember);

            UpdateInfoService.addUpdateInfo('', 'members', selectedMember.name, "", function(resp) {
              if(resp) {
                updateIssue();
              }
            });

          }
        }

        /////////////////////////// COMMENTS ///////////////////////////////

        function addNewComment(newCommentText)
        {
            var issueid = vm.card._id;

            var commentData = {
              commentText: newCommentText,
              author: authentication.currentUser().id
            };

            UpdateInfoService.addUpdateInfo('', 'comments', commentData.commentText, "");

            apilaData.addIssueCommentById(issueid, commentData)
            .success(function(data) {

              //only updating frontend view with our comment so we can use our user data
              var newComment = createComment(newCommentText, data.createdOn);
              newComment._id = data._id;
              vm.card.comments.push(newComment);

            }).error(function(data) {
              $log.debug("Error while adding comment");
            });


        }

        ///////////////////////////// UPDATE ////////////////////////////////

        function updateIssue(type) {

          vm.card.title = vm.card.name;

          var oldResponsibleParty = oldData.responsibleParty._id ?
                                 oldData.responsibleParty._id : oldData.responsibleParty;

          // Remember if responsible party changes
          if(oldResponsibleParty !== vm.card.responsibleParty) {
            vm.card.oldResponsibleParty = oldResponsibleParty;
          }

          // Set author Id if it is an user object but api needs just an _id
          var populatedCard = angular.copy(vm.card);

          setAuthorId(populatedCard.comments);
          setAuthorId(populatedCard.finalPlan);
          setAuthorId(populatedCard.checklists);

          populatedCard.submitBy = populatedCard.submitBy._id;

          apilaData.updateIssue(vm.card._id, populatedCard)
          .success(function(response) {
            vm.card.addedMember = "";
            vm.card.updateField = "";
            updateMainFields(type);
          })
          .error(function(err) {
            $log.debug(err);
          });
        }

        ///////////////////////// UPDATE MAIN FIELDS //////////////////////////

       function removeDueDate() {
         vm.card.due = "";

         var currdue = vm.card.currdue;

          if(isNaN(vm.card.currdue)) {
           currdue = moment(vm.card.currdue).toDate().getTime();
          }

          UpdateInfoService.addUpdateInfo('', 'due', '', currdue, function(resp) {
            if(resp) {
              updateIssue();
            }
          })
          vm.card.currdue = '';

        }

        function updateMainFields(type) {

          if(type === 'responsibleParty') {
            return;
            if(vm.selectedItem.value !== oldData.responsibleParty._id) {
              //UpdateInfoService.addUpdateInfo('responsibleParty', vm.selectedItem.display, oldData.responsibleParty.name);

            }
          }

          var oldValue = oldData[type];
          var newValue = vm.card[type];

          // we changed it
          if(oldValue !== newValue) {
            UpdateInfoService.addUpdateInfo('', type, newValue, oldValue);
          }
        }

        function changeResponsibleParty(type) {

          var responsibleParty = oldData.responsibleParty._id ? oldData.responsibleParty._id : oldData.responsibleParty;

          if(vm.selectedItem && (vm.selectedItem.value !== responsibleParty)) {

            var data = {
              responsibleParty: vm.selectedItem.value,
              issue: vm.card
            };

            vm.card.responsibleParty = vm.selectedItem.value;
            UpdateInfoService.addUpdateInfo('', 'responsibleParty', vm.selectedItem.display, oldData.responsibleParty.name);

            var newList = _.find(vm.board.lists, {name: vm.selectedItem.display});
            var oldList = _.find(vm.board.lists, {name: vm.oldSelectedItem.display});

            oldList.idCards = _.without(oldList.idCards, vm.card.id);

            var hasMember = _.find(vm.card.idMembers, {_id: vm.oldSelectedItem.value})

            //we push it to the new list if it's not confidential, or it is but has a member in it
            if(hasMember || !vm.card.confidential) {
              newList.idCards.push(vm.card.id);
            }
            
            //update activity score

            var stats = BoardService.getStats();

            if(vm.card.flag) {
              stats.numIssues--;
            } else {
              stats.numIssues--;
              stats.numActiveIssues--;
            }

            BoardService.setStats(stats);

            apilaData.sendResponsiblePartyNotification(vm.card._id, data)
            .success(function(resp) {
               vm.updateIssue(type);
               closeDialog();
            })
            .error(function(err) {
              console.log(err);
               vm.updateIssue(type);
               closeDialog();
            });

          }

        }

        function memberUpdate(selectedMember) {
          vm.card.deletedMember = selectedMember;

          UpdateInfoService.addUpdateInfo('', 'members', "" , selectedMember, function(resp) {
            vm.card.deletedMember = true;
            updateIssue();
          });

        }

        function updateLabel(label) {

          apilaData.updateIssueLabel(vm.communityId, label.name, label)
          .success(function(resp) {
            $window.location.reload();
          })
          .error(function(err) {
            $log.debug(err);
          });
        }

        //Update due date
        $scope.$watch('vm.card.currdue', function() {
          if(unchangedDueDate !== vm.card.currdue) {

            if(vm.card.currdue) {
              vm.card.due = vm.card.currdue;

              UpdateInfoService.addUpdateInfo('', 'due', vm.card.currdue, "", function(resp) {
                if(resp) {
                  vm.updateIssue();
                }
              });
            }

          }
        });

        ////////////////////////////// OTHER ///////////////////////////////

        function exportIssue() {

          apilaData.issuePopulateOne(vm.card._id)
          .success(function(resp) {
            vm.card.checklists = resp.checklists;

            vm.card.responsibleParty = vm.responsibleParty;

            exportIssueDetail.exportPdf(vm.card);

            UpdateInfoService.addUpdateInfo('', 'export', vm.card.title, "");

          });

        }

        apilaData.issuePopulateOne(vm.card._id)
        .success(function(resp) {
          vm.card.finalPlan = resp.finalPlan;

          vm.card.checklists = resp.checklists;
          vm.responsibleParty = resp.responsibleParty;

          LabelsService.setViewModel(vm);

          vm.submitBy = resp.submitBy;

          vm.createdIssue = resp.submitBy.name + " created " + vm.card.title;

          vm.selectedItem = {
            value: resp.responsibleParty._id,
            display: resp.responsibleParty.name
          };

          vm.oldSelectedItem = angular.copy(vm.selectedItem);

        });

        function editPermissions(type) {
          if(vm.userRole) {

            if(vm.userRole === 'boss') {
              return false;
            }

            if(vm.userRole === 'directors') {
              return false;
            }

            if(vm.userRole === 'minions' || vm.userRole === 'creator') {

              if(!vm.card.responsibleParty._id) {
                return true;
              }

              // if our own issue
              if(vm.userid === vm.card.responsibleParty._id) {
                if(type === 'status' || type === 'responsibleParty') {
                  return true;
                } else {
                  return false;
                }
              } else {
                return true;
              }

            }
          }

          return true;
        }


        function changeStatus() {

          //if we switched it to Close and we dont have a final plan show Error
          if(vm.card.status === "Closed" && vm.card.finalPlan.length <= 0) {
            vm.card.status = oldData.status;
            showToast("You must write at least one final plan item before closing");
            return;
          }

          if(vm.card.status === "Shelved") {
            vm.card.shelvedDate = new Date();
          }

          vm.updateIssue();

          var stats = BoardService.getStats();

          if(vm.card.status === "Shelved" || vm.card.status === "Closed") {

            updateIssueCount(--vm.count);

            if(vm.card.flag) {
              stats.numIssues--;
            } else {
              stats.numIssues--;
              stats.numActiveIssues--;
            }

          } else {
            updateIssueCount(++vm.count);
            
            if(vm.card.flag) {
              stats.numIssues++;
            } else {
              stats.numIssues++;
              stats.numActiveIssues++;
            }

          }

          BoardService.setStats(stats);

          // delete card
          var cardList = getCardList();

          cardList.idCards.splice(cardList.idCards.indexOf(vm.card.id), 1);
          vm.board.cards.splice(vm.board.cards.indexOf(vm.card), 1);
        }

        function loadIssueCount(id) {

            apilaData.openIssuesCount(userid, id)
            .success(function(count) {

              vm.count = count;

              if(count <= 0) {
                msNavigationService.saveItem('fuse.issues', { badge: null });
                return;
              }

              msNavigationService.saveItem('fuse.issues', {
                badge: {
                  content:  count,
                  color  : '#F44336'
                }
              });
            })
            .error(function(count) {
            });
        }

        function openImage(url){
          $mdDialog.show({
                controllerAs: 'vm',
                controller: 'ImageViewController',
                preserveScope: true,
                autoWrap: true,
                skipHide: true,
                templateUrl: 'app/main/issues/dialogs/card/image-view/imageView.html',
                resolve: {
                  imgUrl: function() {
                    return url;
                  }
                }
              });
         }

         function closeDialog()
         {
             $mdDialog.hide();
         }

         function getCardList()
         {
             var response;
             for ( var i = 0, len = vm.board.lists.length; i < len; i++ )
             {
                 if ( vm.board.lists[i].idCards.indexOf(vm.card.id) > -1 )
                 {
                     response = vm.board.lists[i];
                     break;
                 }
             }
             return response;
         }

         function removeCard(ev)
         {
             var confirm = $mdDialog.confirm({
                 title              : 'Remove Card',
                 parent             : $document.find('#issues'),
                 textContent        : 'Are you sure want to remove card?',
                 ariaLabel          : 'remove card',
                 targetEvent        : ev,
                 clickOutsideToClose: true,
                 escapeToClose      : true,
                 ok                 : 'Remove',
                 cancel             : 'Cancel'
             });

             $mdDialog.show(confirm).then(function ()
             {
                 var cardList = getCardList();

                 //delete that issue
                 apilaData.deleteIssue(vm.card._id)
                 .success(function(d) {

                   cardList.idCards.splice(cardList.idCards.indexOf(vm.card.id), 1);
                   vm.board.cards.splice(vm.board.cards.indexOf(vm.card), 1);

                   updateIssueCount(--vm.count);

                 })
                 .error(function(d) {
                   $log.debug(d);
                 });

             }, function ()
             {
                 // Canceled
             });
         }

         function wordCloud() {
           $mdDialog.show({
                 controllerAs: 'vm',
                 controller: 'WordCloudController',
                 preserveScope: true,
                 autoWrap: true,
                 skipHide: true,
                 templateUrl: 'app/main/issues/dialogs/wordCloud/wordCloud.html',
                 locals: {
                   issue: vm.card
                 }
               });
         }

         function setAuthorId(subdocument) {
           subdocument.forEach(function(doc) {
             if(doc.author && doc.author._id) {
               doc.author = doc.author._id;
             }
           });
         }

         function downloadedInfo(name) {
           var extension = name.split('.').pop();

           if(extension) {
             UpdateInfoService.addUpdateInfo('', 'downloaded', extension, "");
           }
         }

         function addFinalPlan() {

           var data = {
             "text" : vm.newFinalPlanText,
             "checklist" : vm.finalPlanChecklist,
             "author" : userid,
             "todoid": authentication.currentUser().todoid,
             "issueName": vm.card.name
           };

           apilaData.addFinalPlan(vm.card._id, data)
           .success(function(response) {

             if(vm.finalPlanChecklist) {
               UpdateInfoService.addUpdateInfo('', 'plan-todo', vm.newFinalPlanText, "");
             } else {
               UpdateInfoService.addUpdateInfo('', 'plan-create', vm.newFinalPlanText, "");
             }


             response.author = {
               _id: vm.userid,
               name: vm.username,
               userImage: authentication.getUserImage()
             };

             vm.card.finalPlan.push(response);
             vm.newFinalPlanText = "";

             if(vm.finalPlanChecklist) {
               showToast("Created a new task in the todo section");
             }
           })
           .error(function(response) {
             $log.debug(response);
           });
         }

         function updatePlan(plan) {

           var copyPlan = angular.copy(plan);
           var oldPlan = _.find(vm.card.finalPlan, {createdOn: plan.createdOn});

           if(oldPlan) {

             copyPlan.author = plan.author._id;

             apilaData.updateFinalPlan(vm.card._id, plan._id, plan)
             .success(function(resp) {

               UpdateInfoService.addUpdateInfo('', 'plan', plan.text, oldPlan.text);

             })
             .error(function(err) {
               $log.debug(err);
             });
           }
         }

         function updateComment(comment) {

           var copyComment = angular.copy(comment);

           var oldComment = _.find(vm.card.comments, {createdOn: comment.createdOn});

           if(oldComment) {
             copyComment.author = comment.author._id;

             apilaData.issueCommentsUpdate(vm.card._id, copyComment)
             .success(function(resp) {
               UpdateInfoService.addUpdateInfo('', 'comment', comment.commentText, oldComment.commentText);
             })
             .error(function(err) {
               $log.debug(err);
             });
           }

         }

        ///////////////////////// HELPER FUNCTIONS ////////////////////////////

        function createComment(text, createdOn) {
          var newComment = {
              idMember: '',
              commentText : text,
              author    : {
                _id: authentication.currentUser().id,
                name: authentication.currentUser().name,
                userImage: authentication.getUserImage()
              },
              createdOn: createdOn
          };


          return newComment;
        }

        function updateLabelStats(labelname, type) {

           var labelStats = BoardService.calculateLabelStats(vm.board.cards);

            if(!labelStats[labelname]) {
              labelStats[labelname] = 0;
            }

            if(type === "add") {
              labelStats[labelname]++;
            } else {
              labelStats[labelname]--;
            }
            
            apilaData.addLabelStats(vm.communityId, labelStats)
            .success(function(resp) {
                console.log(resp);
            })
            .error(function(err) {
                console.log(err);
            });
        }

        function isLabelInCard(name) {

          var label = _.find(vm.card.labels, {name: name});

          return (label) ? true : false;
        }

        function updateIssueCount(count) {

            if(count <= 0) {
              msNavigationService.saveItem('fuse.issues', { badge: null });
              return;
            }

            msNavigationService.saveItem('fuse.issues', {
              badge: {
                content:  count,
                color  : '#F44336'
              }
            });
        }

        function setUserRole() {
          if(vm.myCommunity.creator.name === vm.username) {
            vm.userRole = "creator";
          } else if(_.find(vm.myCommunity.directors, {"name" : vm.username}) !== undefined) {
            vm.userRole = "directors";
          } else if(_.find(vm.myCommunity.minions, {"name" : vm.username}) !== undefined) {
            vm.userRole = "minions";
          }

          if(vm.myCommunity.boss.name === vm.username) {
            vm.userRole = "boss";
          }
        }

        function showToast(msg) {
          $mdToast.show(
            $mdToast.simple()
            .textContent(msg)
            .position("top right")
            .hideDelay(3000)
          );
        }

        ///////////////////////// THEME FUNCTIONS /////////////////////////////

        function createFilterFor(query)
        {
            var lowercaseQuery = angular.lowercase(query);
            return function filterFn(item)
            {
                return angular.lowercase(item.name).indexOf(lowercaseQuery) >= 0;
            };
        }


    }
})();

