(function() {
  'use strict';

  angular
    .module('app.dashboard')
    .controller('CreateCommunityController', CreateCommunityController);

  /** @ngInject */
  function CreateCommunityController($mdDialog, apilaData, authentication, $log, $mdToast, $window, activeEmail) {

    var vm = this;

    // Data
    vm.form = {};

    vm.hasCanceledCommunity = false;

    vm.username = authentication.currentUser().name;
    vm.userid = authentication.currentUser().id;

    vm.restoreCommunity = false;

    vm.gettingCreated = false;

    //Functions
    vm.closeDialog = closeDialog;
    vm.addCommunity = addCommunity;
    vm.recoverCommunity = recoverCommunity;

    // preload data if we have canceledCommunity
    apilaData.hasCanceledCommunity(vm.userid)
      .success(function(response) {
        vm.canceledCommunity = response;
        vm.hasCanceledCommunity = true;
      })
      .error(function(err) {
        $log.debug(err);
      });

    ///////////////////// PUBLIC FUNCTIONS /////////////////////////////////

    function addCommunity() {
      vm.form.communityMembers = [];
      vm.form.pendingMembers = [];

      vm.form.username = vm.username;

      if (!vm.form.name) {
        vm.nameError = "Community name must be specified";
        return;
      }

      vm.gettingCreated = true;

      Stripe.card.createToken(vm.cardInfo,
        function(status, response) {

          if (status !== 200) {
            processErrors(response.error);
          } else {
            saveCreditCard(response);
          }

        });

    }

    function recoverCommunity() {

      vm.gettingCreated = true;

      Stripe.card.createToken(vm.cardInfo,
        function(status, response) {

          if (status !== 200) {
            processErrors(response.error);
          } else {
            restoreCommunity(response);
          }

        });
    }

    function closeDialog() {
      $mdDialog.hide();
    }

    ///////////////////// PRIVATE FUNCTIONS /////////////////////////////////

    function saveCreditCard(response) {
      apilaData.saveCreditCard(vm.userid, response)
        .success(function(response) {

          apilaData.addCommunity(vm.form)
            .success(function(d) {
              closeDialog();

              showToast("Community has been created!");

              $window.location.reload();
            })
            .error(function(d) {
              $log.debug(d);
            });
        })
        .error(function(response) {
          $log.debug(response);
        });
    }

    function restoreCommunity(response) {
      apilaData.saveCreditCard(vm.userid, response)
        .success(function(response) {

          //restore community
          apilaData.restoreCommunity(vm.userid, vm.canceledCommunity._id)
            .success(function(response) {

              closeDialog();
              showToast("Community has been restored!");

              $window.location.reload();

            })
            .error(function(response) {
              $log.debug(response);
            });
        })
        .error(function(response) {
          $log.debug(response);
        });
    }

    function processErrors(error) {
      if (error.param === "card[number]") {
        vm.cardNumberError = "Invalid credit card number";
      } else if (error.param === "card[exp_month]") {
        vm.expMonthError = "Invalid Expiration Month entered please select between 01 - 12";
      } else if (error.param === "card[exp_year]") {
        vm.expYearError = "Invalid Expiration Year entered";
      }
    }


    function showToast(msg) {
      $mdToast.show(
        $mdToast.simple()
        .textContent(msg)
        .position("top right")
        .hideDelay(3000)
      );
    }


  }

})();

(function ()
{
    'use strict';

    angular
        .module('app.dashboard')
        .controller('BuildingSetupController', BuildingSetupController);

    /** @ngInject */
    function BuildingSetupController($mdDialog, apilaData, $mdToast, $log, authentication, community) {

      var vm = this;

      // Data
      vm.community = community;
      vm.contactInfo = {};

      vm.roomList = _.flatten(_.map(vm.community.roomStyle, "rooms"));

      vm.floors = community.floors;

      updateContactAndRoomInfo("floors");

      // Functions
      vm.closeDialog = closeDialog;
      vm.save = save;
      vm.getMatches = getMatches;
      vm.updateContactAndRoomInfo = updateContactAndRoomInfo;
      vm.updateFloors = updateFloors;

      function save() {
        apilaData.updateContactAndRoomInfo(vm.community._id, vm.community)
        .success(function(resp) {
            updateFloors();
        })
        .error(function(err) {
          $log.debug(err);
        });

        closeDialog();
      }

      function updateContactAndRoomInfo(type) {

        vm.contactInfo.rooms = vm.community.rooms;

        if(type === "floors") {

          var newFloors = vm.community.numFloors - vm.floors.length;

          if(vm.floors.length === 0) { //create floors for first time
            vm.floors = generateFloors(vm.community.numFloors, 0);

          } else if(newFloors > 0) {  // adding new floors
            vm.floors = vm.floors.concat(generateFloors(newFloors, vm.floors.length));

          } else if(newFloors < 0) { // removing new floors
            vm.floors.splice(newFloors);

          } if(vm.community.numFloors === 0) { // reset floors
            vm.floors = [];
          }

        }

        vm.contactInfo.floors = vm.floors;

      }

      function getMatches(text) {

        if(text === null) {
          return vm.roomList;
        }

        var sortedRooms = _.sortBy(vm.roomList, function(room) {
          return +(room.replace(/\D/g, '')); //remove letters and convert to number
        });

        var textLower = text.toLowerCase();

        var ret = sortedRooms.filter(function (d) {
          if(d) {
            return d.toLowerCase().indexOf(textLower) > -1;
          }
        });

        return ret;
      }

      function updateFloors() {

        apilaData.updateFloor(vm.community._id, vm.floors)
        .success(function(resp) {
          $log.debug(resp);
        })
        .error(function(err) {
          $log.debug(err);
        })
      }

      function closeDialog() {
          $mdDialog.hide();
      }

      function generateFloors(numFloors, startCount) {

        var floorRange = _.range(numFloors);

        var floors = _.map(floorRange, function(floorNumber) {
         return {
           floorNumber: floorNumber + startCount,
           startRoom: null,
           endRoom: null
         }});

         return floors;
      }

    }


})();

