(function() {

  'use strict';

  angular
    .module('app.dashboard')
    .service('MemberService', MemberService);


  /** @ngInject */
  function MemberService(apilaData, $mdDialog, $log) {

    var pendingMemberTable = null;
    var communityMemberTable = null;
    var communityId = null;

    function acceptMember(index, member)
    {
      //remove it from pending table
      pendingMemberTable.splice(0, 1);

      var data = {};
      data.member = member[3];

      var addedMember = member.concat([false, false, true, false, "Minion", ""]);

      apilaData.acceptMember(data, communityId)
      .success(function(d) {
        member.splice(member.length-1, 1);
        communityMemberTable.push(addedMember);
      })
      .error(function(d) {
        $log.debug(d);
      });
    }

    function declineMember(index, memberid)
    {
      var data = {
        "member" : memberid
      };

      apilaData.declineMember(data, communityId)
      .success(function(d) {
        pendingMemberTable.splice(index, 1);
      })
      .error(function(d) {
        $log.debug(d);
      });
    }

    function removeMember(userid, name) {

      var confirm = createConfirmDialog(name);

      $mdDialog.show(confirm).then(function() {

        apilaData.removeMember(communityId, userid)
          .success(function(response) {
            removeMemberFromTable(userid);
          })
          .error(function(response) {
            $log.debug(response);
          });

      });

    }

    function addRole(user, type) {
      var userId = user;

      var data = {
        "type" : type
      };

      apilaData.addRole(communityId, userId, data)
      .success(function(response) {
        $log.debug("Role added " + response);
      })
      .error(function(response) {
        $log.debug("Couldn't add a role");
      });
    }

    function setData(pendingMember, communityMember, id) {
      pendingMemberTable = pendingMember;
      communityMemberTable = communityMember;
      communityId = id;
    }

    ///////////////////// HELPER FUNCTIONS /////////////////////////////////

    function removeMemberFromTable(userid) {
      for(var i = 0; i < communityMemberTable.length; ++i) {
        if(communityMemberTable[i][3] === userid) {
          communityMemberTable.splice(i, 1);
          return;
        }
      }
    }

    function createConfirmDialog(name) {
      var confirm = $mdDialog.confirm()
         .title('Are you sure you want to remove the user ' + name)
         .ariaLabel('Lucky day')
         .ok('Yes')
         .cancel('Cancel');

      return confirm;
    }

    return {
      acceptMember : acceptMember,
      declineMember : declineMember,
      removeMember : removeMember,
      addRole : addRole,
      setData : setData
    };

  }

})();

(function ()
{
    'use strict';

    angular
        .module('app.dashboard')
        .service('LogoService', LogoService);

    /** @ngInject */
    function LogoService(apilaData, Upload, authentication, imageData, $timeout, $mdToast, $mdDialog) {

      var uploadFiles = function(file, errFiles, community) {

        if(errFiles && errFiles.length > 0) {
            var error = errFiles[0].$error;

            if(error === 'maxSize') {
                 $mdToast.show(
                    $mdToast.simple()
                    .textContent("Logo File is too Large (over 5MB)")
                    .position("top right")
                    .hideDelay(3000)
                );
            }

            return;
        }

        var uploadUrl = apilaData.getApiUrl() + '/api/communities/'+ community._id + '/logo';



        if (file) {

            if(file.type === 'image/jpg' || file.type === 'image/jpeg' || file.type === 'image/png') {

                file.upload = Upload.upload({
                    url: uploadUrl,
                    data: {file: file},
                    headers: {
                        Authorization: 'Bearer ' + authentication.getToken()
                    }
                });

                file.upload.then(function (response) {
                    $timeout(function () {
                        file.result = response.data;
                        community.logo = response.data.url;

                        imageData.loadLogo(community.logo);

                        cropDialog();

                    });
                }, function (err) {

                    var errMsg = angular.isString(err) ? err : err.data.message;

                    errMsg = errMsg || "Sorry we are unable to upload the specified file";

                    $mdToast.show(
                        $mdToast.simple()
                        .textContent(errMsg)
                        .position("top right")
                        .hideDelay(3000)
                    );
                });   
            } else {
                $mdToast.show(
                    $mdToast.simple()
                    .textContent("You can upload only a .jpg or a png image")
                    .position("top right")
                    .hideDelay(3000));
            }
        }

         function cropDialog() {
          $mdDialog.show({
              controller         : 'CropLogoController',
              controllerAs       : 'vm',
              templateUrl        : 'app/main/dashboard/dialogs/crop_logo/crop_logo.html',
              locals             : {community: community},
              clickOutsideToClose: true
          });
        }

      };


      return {
        uploadFiles : uploadFiles
      };
    }


})();

