(function() {

  'use strict';

  angular
    .module('app.calendar')
    .service('HelperCalendar', HelperCalendar);


  /** @ngInject */
  function HelperCalendar(apilaData, $log) {

    function createStandardEvent(text, startDate, color) {
        return {
            title: text,
            start: startDate,
            end: null,
            stick: true,
            color: color
        };
    }

    function formatResidentList(resid) {
        var name = resid.aliasName ? resid.aliasName : resid.firstName;

        return { value:resid._id, display: name + " " + resid.lastName };
    }

    function formatExport(calendar, events) {
        var columns = ["Resident", "Date", "Reason", "Location", "Doctor", "Transportation"];

        var month = calendar.getDate().format("MMMM");

        //first sort the events by date
        var sortedEvents = _.sortBy(events, function(p) {
            return p.start;
        });

        //fitler out just the events from the selected month and then map to table
        var rows = _.map(_.filter(sortedEvents, function(o) {
            return moment(o.start).format("MM") === calendar.getDate().format("MM") && o.cancel === false;
        }),

        function(d) {
            var arr = [];

            var name = "";
            if (d.residentGoing != null) {
            name = d.residentGoing.firstName + " " + d.residentGoing.lastName;
            }

            arr.push(name);
            arr.push(moment(d.start).format("MM/DD hh:mm A"));
            arr.push(d.reason);
            arr.push(d.locationName.name || d.locationName);
            arr.push(d.locationDoctor);
            arr.push(d.transportation);

            return arr;
        });

        return {
            month: month,
            columns: columns,
            rows: rows
        };
    }

    function addTasks(tasks, events, dateString, currMonth) {

        var NUM_DAYS = 3;

        tasks.forEach(function(task) {

            var numOfDays = moment().daysInMonth();

            if(dateString) {
              numOfDays = moment(dateString).daysInMonth();
            }

            //hourly tasks
            if(task.occurrence === 0) {

              for(var i = 0; i < NUM_DAYS; ++i) {

                var currDate = moment();

                var repeatTime = "";

                if(task.hourStart >= 0) {
                  currDate.set({hour: task.hourStart});

                  repeatTime = "x " + (task.hourEnd - task.hourStart + 1) + " ";
                }

                var event = createStandardEvent(repeatTime + task.text, currDate.add(i, 'days'), "#EF6C00");

                var currWeek = currDate.isoWeekday() - 1;

                if(task.activeDays[currWeek]) {
                  events.push(event);
                }

              }

            } else if(task.occurrence === 1) {

              for(var i = 0; i < NUM_DAYS; ++i) {

                var currDate = moment();

                if(task.startTime) {
                  var startActive = moment(task.startTime);

                  currDate.set({hour: startActive.get('hour'), minute: startActive.get('minute')});
                }

                var event = createStandardEvent(task.text, currDate.add(i, 'days'), "#EF6C00");

                var currWeek = currDate.isoWeekday() - 1;

                if(task.activeDays[currWeek]) {
                  events.push(event);
                }

              }

            } else if(task.occurrence === 2) {
              
                  for(var i = 0; i < numOfDays; ++i) {

                    var startOfMonth = moment().startOf('month');

                    if(dateString) {
                      startOfMonth = moment(dateString).startOf('month');
                    }

                    if(task.weekStartTime) {
                      var startActive = moment(task.weekStartTime);

                      startOfMonth.set({hour: startActive.get('hour'), minute: startActive.get('minute')});
                    }

                    var event = createStandardEvent(task.text, startOfMonth.add(i, 'days'), "#EF6C00");

                    var currWeek = startOfMonth.isoWeekday() - 1;

                    var weekOfMonthNum = weekOfMonth(startOfMonth) - 1;

                    var rightDay = task.selectDay ? currWeek === mapDays[task.selectDay] : currWeek === 0;

                    if(task.everyWeek && rightDay) {
                      events.push(event);
                    } else if(task.activeWeeks[weekOfMonthNum] && rightDay) {
                      events.push(event);
                    }

                  }
            } else if(task.occurrence === 3) {

              if(task.activeMonths[currMonth - 1]) {
                var startOfMonth = moment().startOf('month');

                if(dateString) {
                  startOfMonth = moment(dateString).startOf('month');
                }

                if(task.selectedWeekDay) {
                  var weekDay = task.selectedWeekDay.toString().split('');

                  var day = parseInt(weekDay[0]) * 7 + parseInt(weekDay[1]);
                  startOfMonth.add(day, 'days');
                }

                if(task.daysInMonth) {
                  startOfMonth.add(task.daysInMonth - 1, 'days');
                }
                
                var event = createStandardEvent(task.text, startOfMonth, "#EF6C00");

                var currWeek = startOfMonth.isoWeekday() - 1;

                events.push(event);

              }

            }
        });

    }

    function weekOfMonth(m) {
        return m.week() - moment(m).startOf('month').week() + 1;
    }

    var mapDays = {
        'Monday': 0,
        'Tuesday': 1,
        'Wednesday': 2,
        'Thursday': 3,
        'Friday': 4,
        'Saturday': 5,
        'Sunday': 6
    };

    return {
        createStandardEvent: createStandardEvent,
        formatResidentList: formatResidentList,
        formatExport: formatExport,
        weekOfMonth: weekOfMonth,
        mapDays: mapDays,
        addTasks: addTasks
    };

  }

})();
          
