(function ()
{
    'use strict';

    angular
        .module('app.pages.auth.forgot-password')
        .controller('ForgotPasswordController', ForgotPasswordController);

    /** @ngInject */
    function ForgotPasswordController(apilaData, $mdToast, $state, $timeout)
    {
        // Data
        var vm = this;

        // Methods
        vm.sendMail = function() {

          if(!vm.form.email) {
            return;
          }

          apilaData.forgotPassword(vm.form.email)
          .success(function(response) {
 
            showToast("Email with password reset has been sent to you", 2000);

            vm.form.email = "";

            //switch after some delay so the user sees the toast
            $timeout(function () {
              $state.go('app.pages_auth_login');
            }, 1500);
          })
          .error(function(response) {
            showToast("Email not found", 2000);
          });
        }

        function showToast(text, time) {
          $mdToast.show(
            $mdToast.simple()
            .textContent(text)
            .position("top right")
            .hideDelay(time)
          );
        }
    }
})();

