(function() {

  angular.module("app.core").service("exportToDo", exportToDo);

  exportToDo.$inject = ['header', 'ToDoService', 'Footer'];

  function exportToDo(header, ToDoService, Footer) {

    var OFFSET_X = 20;
    var pageNum;

    function exportPdf(data) {

      pageNum = 1;

      var doc = new jsPDF('p', 'pt');

      //config stuff
      doc.setFont("courier");
      doc.setFontType("normal");
      doc.setFontSize(12);
      doc.setLineWidth(1);

      var currPosition = 20;

      currPosition = header.drawHeader(doc, data, currPosition);

      // Draw user name
      OFFSET_X = 10;
      currPosition += 30;
      doc.setFontSize(14);
      doc.text("To-Dos for " + data.username, OFFSET_X, currPosition);
      doc.setFontSize(12);
      currPosition += 30;

      var pos = currPosition;

      Footer.drawFooter(doc, pageNum);
      pageNum++;

      data.tasks.forEach(function(task, i) {
        pos = drawTask(doc, task, currPosition, pos);
      });

      doc.save('todo.pdf');
    }

    function heightOfTask(doc, task) {

      var pos = 0;

      var text = doc.splitTextToSize(task.text, 560);

      if(text.length >= 2) {
          pos += text.length * 11.6;
      }

      pos += 50;

      return pos;

    }

    function drawTask(doc, task, currPosition, pos) {

        currPosition = pos + 30;

        if(currPosition + heightOfTask(doc, task) > doc.internal.pageSize.height) {
          currPosition = 20;
          doc.addPage();
          Footer.drawFooter(doc, pageNum);
          pageNum++;
        }

        //task description
        var text = doc.splitTextToSize(task.text, 560);
        doc.text(text, OFFSET_X, currPosition);

        if(text.length >= 2) {
          currPosition += text.length * 11.6;
        }

        var occurrence = ToDoService.occurrenceMap[task.occurrence.toString()] + " | ";
        var availability = "";

        switch(task.occurrence) {
            case 0:
                occurrence += ToDoService.convertToPm(task.hourStart) + ' - ' + ToDoService.convertToPm(task.hourEnd);
            break;
            case 1:
                occurrence += ToDoService.showActiveDays(task);

                if(task.startTime && task.endTime) {
                  availability = moment(task.startTime).format("hh A") + " - " + moment(task.endTime).format("hh A") ;
                }
            break;
            case 2:
                occurrence += ToDoService.showActiveWeeks(task);

                if(task.selectedWeekDay && task.weekStartTime && task.weekEndTime) {
                  availability = task.selectedWeekDay + " | " + moment(task.weekStartTime).format("hh A") + " - " + moment(task.weekEndTime).format("hh A");
                }
            break;
            case 3:
                occurrence += ToDoService.showActiveMonths(task);

                if(task.daysInMonth) {
                  availability = task.daysInMonth + "th of the Month";
                } else if(task.selectedWeekDay) {
                  var selectedWeekArr = task.selectedWeekDay.toString().split('');


                  availability = ToDoService.weekMap[selectedWeekArr[0]] + " " + ToDoService.dayMap[selectedWeekArr[1]];
                }
            break;
        }

        currPosition += 12;
        doc.text(occurrence, OFFSET_X, currPosition);

        //task availability interval
        if(availability) {
          currPosition += 12;
          doc.text(availability, OFFSET_X, currPosition);
        }

        currPosition += 12;
        doc.text("Completed " + task.completed.length + " times", OFFSET_X, currPosition);

        currPosition += 12;

        var mostRecently = "";

        if(task.notCompleted.length > 0) {
          mostRecently = " most recently on (";

          mostRecently += _.takeRight(task.notCompleted, 3).map(function(c) {
            return moment(c.updateOn).format("MMMM d YYYY");
          }).toString();

          mostRecently += ")";
        }

         doc.text("Not Completed " + task.notCompleted.length + " times" + mostRecently, OFFSET_X, currPosition);

         return currPosition;

    }


    return {
      exportPdf : exportPdf
    };

  }

})();

