(function() {
  angular.module("app.core").service("exportBlankCarePlan2", exportBlankCarePlan2);

  exportBlankCarePlan2.$inject = ['$http', 'apilaDoc', 'header', '$filter', 'imageData', 'Footer'];

  function exportBlankCarePlan2($http, apilaDoc, header, $filter, imageData, Footer) {

    function exportPdf(resident) {

        apilaDoc.createDoc();

        currPosition = header.drawHeader(apilaDoc.getDoc(), resident, 20);

        apilaDoc.setY(currPosition + 50);

        $http.get('/app/common/services/exports/json/residentBlankCarePlanMap.json')
        .success(function(jsonFieldsData) {
            
            resident = {
                firstName: 'Jon',
                lastName: 'Doe',
                notes: ' text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text  text text  text  text  text  text '
            };

            //Loop through properties of the resident
            for(var key in jsonFieldsData) {
                if(jsonFieldsData.hasOwnProperty(key)) {

                    var res = jsonFieldsData[key];

                    switch(res.type) {
                        case 'oneLine':
                            apilaDoc.drawLineField(res, res.display, false,  resident[key], res.layout);
                        break;

                        case 'multiLine':
                            apilaDoc.drawLineField(res, res.display, true,  resident[key], res.layout);
                        break;

                        case 'checkbox':
                            apilaDoc.drawCheckboxField(res, res.display, "", res.layout);
                        break;

                        case 'selectBox':
                            apilaDoc.drawSelectBoxField(res, res.display, res.options, res.layout);
                        break;

                        case 'slider':
                            apilaDoc.drawSliderField(res.display, res.numCircles);
                        break;
                    }

                }
            }

            apilaDoc.saveDoc('ResidentBlankCarePlan2.pdf');
        });

    }

    return {
        exportPdf: exportPdf
    }
  }

  })();
