(function() {
  angular.module("app.core").service("exportAppointDetail", exportAppointDetail);

  exportAppointDetail.$inject = ['imageData', 'graphs', 'header', 'Footer'];

  function exportAppointDetail(imageData, graphs, header, Footer) {

    var OFFSET_X = 20;
    var pageNum;

    function exportPdf(name, data) {
      var doc = new jsPDF('p', 'pt', 'letter');

      pageNum = 1;

      //config stuff
      doc.setFont("courier");
      doc.setFontType("normal");
      doc.setFontSize(12);
      doc.setLineWidth(1);

      Footer.drawFooter(doc, pageNum);
      pageNum++;

      var currPosition = 20;
      var coordsPerLetterAt10 = (594 / 98);
      var coordsPerLetterAt12 = (610 / 84);
      var coordsPerLetterAt14 = (594 / 70);

      currPosition = header.drawHeader(doc, data, currPosition);
      currPosition = appointmentInfo(doc, data, currPosition);

      var commentPos = 0;
      var numAdditionalComments = 0;
      var textHeight = 15;
      var textWidth = 7;

      var commentAvailableSpace = doc.internal.pageSize.height - 310;

      //calculate the size of comments
      var spaceOfComments = getCommentsHeight(doc, data.calendar.appointmentComment, textHeight);

      doc.setFontSize(12);

      if (spaceOfComments > commentAvailableSpace) {
        doc.setFontSize(11);
        textHeight = 12.5;
        textWidth = 6;

        spaceOfComments = getCommentsHeight(doc, data.calendar.appointmentComment, textHeight);

        if (spaceOfComments > commentAvailableSpace) {
          doc.setFontSize(10);
        }
      }

      data.calendar.appointmentComment.forEach(function(comment, i) {
        commentPos = drawComment(doc, comment, commentPos, textHeight, textWidth, numAdditionalComments);
      });

      doc.setFontSize(12);

      if (numAdditionalComments > 0) {
        doc.text(numAdditionalComments.toString() + "  additional comments", 40, 290);
      }

      doc.text("Notes", 10, 408 + (24 * -1));
      doc.line(("Notes".length * coordsPerLetterAt12) + 10, 408 + (24 * -1), 600, 408 + (24 * -1))
      doc.line(10, 408 + (24 * 0), 600, 408 + (24 * 0))
      doc.line(10, 408 + (24 * 1), 600, 408 + (24 * 1))
      doc.line(10, 408 + (24 * 2), 600, 408 + (24 * 2))
      doc.line(10, 408 + (24 * 3), 600, 408 + (24 * 3))
      doc.line(10, 408 + (24 * 4), 600, 408 + (24 * 4))
      doc.line(10, 408 + (24 * 5), 600, 408 + (24 * 5))
      doc.line(10, 408 + (24 * 6), 600, 408 + (24 * 6))

      doc.text("RX", 10, 408 + (24 * 8));
      doc.line(("RX".length * coordsPerLetterAt12) + 10, 408 + (24 * 8), 600, 408 + (24 * 8))
      doc.line(10, 408 + (24 * 9), 600, 408 + (24 * 9))
      doc.line(10, 408 + (24 * 10), 600, 408 + (24 * 10))
      doc.line(10, 408 + (24 * 11), 600, 408 + (24 * 11))
      doc.line(10, 408 + (24 * 12), 600, 408 + (24 * 12))
      doc.line(10, 408 + (24 * 13), 600, 408 + (24 * 13))

      doc.text("Physician", 10, 408 + (24 * 15));
      doc.text("Signature", 10, 420 + (24 * 15));
      doc.line(("Signature".length * coordsPerLetterAt12) + 10, 420 + (24 * 15), 400, 420 + (24 * 15))

      doc.text("Date", 410, 420 + (24 * 15));
      doc.line(("Date".length * coordsPerLetterAt12) + 410, 420 + (24 * 15), 600, 420 + (24 * 15))


      //draw the graphs page
      doc.addPage();

      Footer.drawFooter(doc, pageNum);
      pageNum++;

      // export config
      doc.setFont("courier");
      doc.setFontType("normal");
      doc.setFontSize(12);
      doc.setLineWidth(1);

      doc.line(305, 0, 305, 800);

      doc.line(0, 201, 610, 201);
      doc.line(0, 397, 610, 397);
      doc.line(0, 593, 610, 593);

      doc.setFontType("bold");
      doc.setTextColor(255, 0, 0);
      doc.text("Diastolic", (610 - ("Diastolic".length * coordsPerLetterAt12)), 614);
      doc.setTextColor(0, 0, 0);

      //it is an array of data, date fields
      graphs.drawGraph(doc, data.calendar.residentGoing.temperature, "Temperature", 6, 10, data.tempUnit);
      graphs.drawGraph(doc, data.calendar.residentGoing.oxygenSaturation, "Oxygen Saturation", 6, 206, '%');
      graphs.drawGraph(doc, data.calendar.residentGoing.pulse, "Pulse", 6, 402, 'BPM');
      graphs.drawGraph(doc, data.calendar.residentGoing.vitalsPain, "Pain", 6, 598, ' ');

      graphs.drawGraph(doc, data.calendar.residentGoing.respiration, "Respiration", 314, 10, 'BPM');
      graphs.drawGraph(doc, data.calendar.residentGoing.weight, "Weight", 314, 206, data.weightUnit);
      graphs.drawGraph(doc, data.calendar.residentGoing.internationalNormalizedRatio, "INR", 314, 402, ' ');
      graphs.drawGraph(doc, data.calendar.residentGoing.bloodPressureDiastolic, "Blood Pressure", 314, 598, 'mmHg');

      doc.save(name);

      OFFSET_X = 20;
    }

    function getCommentsHeight(doc, comments, textHeight) {
      var sum = 0;

      comments.forEach(function(comment, i) {

        var splitText = doc.splitTextToSize(comment.commentText, 420);

        sum += splitText.length * textHeight;

      });

      return sum;
    }

    function appointmentInfo(doc, data, currPosition) {
      var coordsPerLetterAt10 = (594 / 98);
      var coordsPerLetterAt14 = (594 / 70);

      var currDay = moment().isoWeekday() - 1;

      var firstName = data.calendar.currentUser.aliasName || data.calendar.currentUser.firstName;
      var fullName = firstName + " " + data.calendar.currentUser.lastName;

      currPosition += 24;

      var locationName = data.calendar.locationName.name || data.calendar.locationName;

      var appointmentTitle = "Appointment for " + fullName + " to " + locationName;
      var appointmentTitleToLocation = "Appointment for " + fullName + " to ";

      var locationPos = (appointmentTitleToLocation.length + 1) * coordsPerLetterAt14;

      doc.setFontSize(14);
      doc.text(appointmentTitle, 10, currPosition);

      // drawing of location data
      if (data.calendar.locationName.name) {
        locationName = data.calendar.locationName.name;

        var number = data.calendar.locationName.formatted_phone_number || 'Phone Number: ';
        var address = data.calendar.locationName.formatted_address || 'Address';

        address = splitAddressInTwo(address);

        var workingHours = 'Working hours';

        if (data.calendar.locationName.opening_hours && data.calendar.locationName.opening_hours.weekday_text) {
          workingHours = data.calendar.locationName.opening_hours.weekday_text[currDay];
        }

        doc.setFontSize(10);
        doc.text(address, locationPos, currPosition + 14);
        doc.text(number, locationPos, currPosition + 38);
        doc.text(workingHours, locationPos, currPosition + 50);
      }

      doc.setFontSize(10);

      currPosition += 14;
      doc.text("Reason:", 90 - ("Reason:".length * coordsPerLetterAt10), currPosition);
      doc.text(data.calendar.reason, 94, currPosition);

      currPosition += 12;
      doc.text("Doctor:", 90 - ("Doctor:".length * coordsPerLetterAt10), currPosition);
      doc.text(data.calendar.locationDoctor, 94, currPosition);

      currPosition += 12;
      var timeFormat = data.calendar.isAm ? 'am' : 'pm';
      var timeDate = moment(data.calendar.appointmentDate).format("Do MMMM YYYY");

      doc.text("Time:", 90 - ("Time:".length * coordsPerLetterAt10), currPosition);
      doc.text(data.calendar.hours + ":" + data.calendar.minutes + " " + timeFormat +
        ", " + timeDate, 94, currPosition);

      currPosition += 12;
      doc.text("Transporting:", 90 - ("Transporting:".length * coordsPerLetterAt10), currPosition);
      doc.text(data.calendar.transportation, 94, currPosition);

      doc.setFontSize(12);

      return currPosition;
    }

    function splitAddressInTwo(address) {
       var addrPart = address.split(',');

        if(addrPart.length >= 2) {
          address = [addrPart[0]];
          addrPart.shift();
          address.push(addrPart.toString());
        }

        return address;
    }

    function drawComment(doc, comment, commentPos, textHeight, textWidth, numAdditionalComments) {

      var splitText = doc.splitTextToSize(comment.commentText, 420);
      var currPageHeight = commentPos + (splitText.length * textHeight) + 200;

      if (currPageHeight < doc.internal.pageSize.height) {

        var commentHeader = comment.author.name + " on " + moment(comment.createdOn).format("DD MMM") + ": ";

        var headerDistance = commentHeader.length * textWidth;

        doc.text(commentHeader, 10, 200 + commentPos);
        doc.text(splitText, 15 + headerDistance, 200 + commentPos);

        commentPos += splitText.length * textHeight;

        return commentPos;

      } else {
        numAdditionalComments++;
      }
    }


    return {
      exportPdf: exportPdf
    };

  }

})();


(function() {
  angular.module("app.core").service("apilaDoc", apilaDoc);

  apilaDoc.$inject = ['imageData', 'graphs', 'header', 'Footer'];

  function apilaDoc(imageData, graphs, header, Footer) {

    var doc = null;

    var currPosX = 20;
    var currPosY = 20;
    var startX = 20;
    var startY = 20;

    var endX = 560;
    var halfPageX = 560 / 2;

    var lineWidth = 20;

    var fontSize = 12;
    var height = 0;
    var pageNum = 1;
    var pagination = true;

    function createDoc() {
        doc = new jsPDF('p', 'pt');

        doc.setFont('times')
        doc.setFontSize(fontSize);

        currPosX = 20;
        currPosY = 20;
        startX = 20;
        startY = 20;

        endX = 560;

        lineWidth = 20;

        fontSize = 12;
        height = doc.internal.pageSize.height;

        if(pagination) {
            Footer.drawFooter(doc, pageNum);
        }
    }

    function getDoc() {
        return doc;
    }

    function saveDoc(name) {
        doc.save(name);
    }

    function setY(y) {
        startY = y;
        currPosY = y;
    }

    function drawLineField(options, fieldName, multi, text, layout) {

        var paddingX = options.paddingX ? parseInt(options.paddingX) : 0;
        var paddingY = options.paddingY ? parseInt(options.paddingY) : 0;

        text = text || '';

        if(!multi) {
            doc.text(currPosX + paddingX, currPosY - 3, fieldName + "    " + text);
        }

        var fieldNameLength = (doc.getStringUnitWidth(fieldName)  * fontSize);
        var linePosX = currPosX + fieldNameLength + 8 + paddingX;

        var lineEnd = endX;
        var lineStart = linePosX;

        if(layout === 'half-1') {
            lineEnd = halfPageX - 10;
        }

        if(layout === 'half-2') {
            lineStart = halfPageX + fieldNameLength + 8;
        }

        doc.line(lineStart + paddingX, currPosY + 2, lineEnd + paddingX, currPosY);

        if(multi) {
            if(!text) {
                currPosX = startX;
                currPosY = nextLinePos();

                doc.line(currPosX + paddingX, currPosY, endX + paddingX, currPosY);

                currPosY = nextLinePos();
                doc.line(currPosX + paddingX, currPosY, endX + paddingX, currPosY);
            } else {

                var offset = 0;

                textArr = doc.splitTextToSize(text, 460);

                textArr.forEach(function(textLine, i) {

                    if(i !== 0) {
                        fieldName = '';
                        doc.line(currPosX, currPosY + offset + 2, endX + paddingX, currPosY + offset + 2);
                    }

                    doc.text(currPosX, currPosY + offset, fieldName + " " + textLine);

                    
                    offset += 15;
                });

                currPosY += offset;
            }
            
        }

        layoutParsing(layout, true, paddingY);
        
    }

    function drawCheckboxField(options, fieldName, text, layout) {

        var paddingX = options.paddingX ? parseInt(options.paddingX) : 0;
        var paddingY = options.paddingY ? parseInt(options.paddingY) : 0;

        doc.text(currPosX + paddingX, currPosY, fieldName);

        currPosY += 15;

        doc.setFontSize(fontSize - 1);
        doc.text(currPosX + 5 + paddingX, currPosY, "Yes     No");
        doc.setFontSize(fontSize);

        layoutParsing(layout, false, paddingY);
    }

    function drawSelectBoxField(opt, fieldName, options, layout) {

        var paddingX = opt.paddingX ? parseInt(opt.paddingX) : 0;
        var paddingY = options.paddingY ? parseInt(options.paddingY) : 0;

        doc.text(currPosX + paddingX, currPosY, fieldName);

        currPosY += 15;

        currPosX += paddingX;

        options.forEach(function(opt, i) {

            var optLength = (doc.getStringUnitWidth(opt)  * fontSize);

            var offsetX = optLength + 15;

            doc.text(currPosX, currPosY, opt);

            currPosX += offsetX;
        });

        layoutParsing(layout, false, paddingY);
        
    }

    function drawSliderField(fieldName, numCircles) {

        doc.text(currPosX , currPosY, fieldName);
        currPosY = nextLinePos();

        doc.line(currPosX + 5, currPosY, (numCircles * 20) + 10, currPosY);

        doc.setFillColor(255,255,255);

        for(var i = 0; i < numCircles; ++i) {
            doc.circle(startX + (i * 20) + 10, currPosY, 5, 'FD');
        }

        currPosY = nextLinePos();
        //layoutParsing(layout, false, paddingY);
    }

    function nextLinePos(offsetY) {

        offsetY = offsetY || 0;

        currPosY += lineWidth + offsetY;
        return currPosY
    }

    function layoutParsing(layout, yPosNotChanged, offsetY) {

        addNewPage(offsetY);

        if(layout === 'full') {
            currPosY = nextLinePos(offsetY);
            currPosX = startX;
        } else if(layout.indexOf('half') !== -1) {

            var pos = layout.split('-')[1];

            if(pos === '1') {
                currPosX = halfPageX;
                currPosY -= yPosNotChanged ? 0 : 15;
            } else {
                currPosY = nextLinePos(offsetY);
                currPosX = startX;
            }
        }
    }

    function disablePagination() {
        pagination = false;
    }

    function addNewPage(offsetY) {

        offsetY = offsetY || 0;

        if(currPosY + offsetY + 80 > height) {
            doc.addPage();

            if(pagination) {
                pageNum++;
                Footer.drawFooter(doc, pageNum);
            }

            currPosX = 20;
            currPosY = 20;
        }
    }

    return {
        createDoc : createDoc,
        drawLineField : drawLineField,
        drawSliderField : drawSliderField,
        drawSelectBoxField : drawSelectBoxField, 
        drawCheckboxField : drawCheckboxField,
        getDoc : getDoc,
        saveDoc : saveDoc,
        setY : setY,
        disablePagination : disablePagination

    }
  }

})();
