(function() {

  angular
      .module('app.core')
      .service('ToDoService', ToDoService);

  function ToDoService() {

    var weekMap = {
      "1": "First",
      "2": "Second",
      "3": "Third",
      "4": "Fourth",
      "5": "Last"
    };

    var dayMap = {
      "1": "Monday",
      "2": "Tuesday",
      "3": "Wednesday",
      "4": "Thursday",
      "5": "Friday",
      "6": "Saturday",
      "7": "Sunday"
    };

    var occurrenceMap = {
        '0': 'Hourly',
        '1': 'Daily',
        '2': 'Weekly',
        '3': 'Monthly'
    };

    var interval = {
      "HOURLY" : 0,
      "DAILY" : 1,
      "WEEKLY" : 2,
      "MONTHLY" : 3
    };

    function convertToPm(value) {
      var americanTime = "";

      americanTime = (value < 12) ? value + " am" :  (value - 12) + ' pm';

      americanTime = (value === 0) ? "12 am" :  americanTime;
      americanTime = (value === 12) ? "12 pm" :  americanTime;

      return americanTime;
    }

     function showActiveDays(task) {
          var daysDesc = "";
          var hourTime = "";
          var days = task.activeDays;
          var count = 0;

          var dayNames = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];

          daysDesc += " (";

          // if standard Monday - Friday work week is selected
          if(days[0] === true && days[1] === true && days[2] === true && days[3] === true && days[4] === true)
          {
            if(!days[5] && !days[6]) {
              return daysDesc + " Mon - Fri ) ";
            }
          }

          // check each day
          for(var i = 0; i < days.length; ++i) {
            if(days[i] === true) {
              daysDesc += " " + dayNames[i] + ",";
              count++;
            }
          }

          // if every day is selected
          if(count === 7) {
            return (task.occurrence === interval.HOURLY) ? hourTime + "( All week )" : "( All week )";
          }

          // remove the last ,
          if(daysDesc[daysDesc.length-1] === ',') {
            daysDesc = daysDesc.slice(0, -1);
          }


          return daysDesc + " ) ";

        }

        function showActiveWeeks(task) {
          var weeksFormated = "( ";
          var weeks = task.activeWeeks;

          var weekNames = ["First", "Second", "Third", "Fourth", "Last"];

          if(task.everyWeek === true) {
            return "( Every Week )";
          }

          // check each day
          for(var i = 0; i < weeks.length; ++i) {
            if(weeks[i] === true) {
              weeksFormated += " " + weekNames[i] + ",";
            }
          }

          weeksFormated = removeLastComma(weeksFormated);

          weeksFormated += " Week of Each Month";

          return weeksFormated + " )";
        }

        function showActiveMonths(task) {
          var monthsFormated = "(";
          var months = task.activeMonths;
          var count = 0;

          var monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sept", "Oct", "Nov", "Dec"];

          // check each day
          for(var i = 0; i < months.length; ++i) {
            if(months[i] === true) {
              monthsFormated += " " + monthNames[i] + ",";
              count++;
            }
          }

          // if every day is selected
          if(count === 12) {
            return "( Every Month )";
          }

          // remove the last ,
          monthsFormated = removeLastComma(monthsFormated);

          return monthsFormated + ' )';
        }

        function removeLastComma(str) {
          if(str[str.length-1] === ',') {
            return str.slice(0, -1);
          } else {
            return str;
          }
        }



    return {
        convertToPm : convertToPm,
        removeLastComma : removeLastComma, 
        showActiveMonths : showActiveMonths,
        showActiveWeeks : showActiveWeeks,
        showActiveDays : showActiveDays,
        weekMap : weekMap,
        dayMap : dayMap,
        occurrenceMap : occurrenceMap,
        interval: interval
    };
  }

})();
