(function() {

    angular
        .module('app.core')
        .service('SearchService', SearchService);

    SearchService.$inject = ['msNavigationService', '$q', '$rootScope'];

    function SearchService(msNavigationService, $q, $rootScope) {

      // The data that is being set through filter/search
      var data = [];

      // The resulting data from the filter/search function
      var resultData = [];

      var searchBy = [];

      var unfiltered = [];

      function setData(d, searchParams) {
        data = d;
        searchBy = searchParams;
      }

      function getUnfiltered() {
        return unfiltered;
      }

      function getResult() {
        return resultData;
      }

      function subscribe(scope, callback) {
        var handler = $rootScope.$on('notifying-service-event', callback);
        scope.$on('$destroy', handler);
      }

      function search(query)
      {

          unfiltered = [];
          var deferred = $q.defer();

          if(searchBy === undefined || searchBy === []) {
            return;
          }

          if ( query )
          {

              resultData = data.filter(function (item)
              {
                console.log(item.title, angular.lowercase(item['title'].toString()).search(angular.lowercase(query)));

                  for(var i = 0; i < searchBy.length; ++i) {
                    if(item[searchBy[i]] !== undefined) {
                      if ( angular.lowercase(item[searchBy[i]].toString()).indexOf(angular.lowercase(query)) !== -1 ) {
                        return true;
                      }
                    }
                  }

                  // searching of nested fields
                  if(item.residentGoing !== undefined) {
                    if ( angular.lowercase(item.residentGoing.firstName + item.residentGoing.lastname).indexOf(angular.lowercase(query)) !== -1 ) {
                      return true;
                    }
                  }

                  return false;

              });

              //resultData = _.uniqBy(resultData, "_id");

          } else if(query === "") {

            resultData = removeDueDate();
          }

          // notify the controller to update their data
          $rootScope.$emit('notifying-service-event');

          deferred.resolve(resultData);

          return deferred.promise;
      }

      //remove due date, temp function while the calendar is sorted out
      function removeDueDate() {
        var tmp = [];
        for(var i = 0; i  < data.length; ++i) {
          if(data[i].color === undefined) {
            tmp.push(data[i]);
          }
        }

        return tmp;
      }

      function collapseSearch() {
        resultData = removeDueDate();
        $rootScope.$emit('notifying-service-event');
      }

      function searchResultClick(item) {
        console.log(item);
      }

      return {
        setData : setData,
        getResult : getResult,
        getUnfiltered : getUnfiltered,
        search : search,
        subscribe : subscribe,
        searchResultClick : searchResultClick,
        collapseSearch : collapseSearch
      };

    }

})();

(function() {

    angular
        .module('app.core')
        .service('imageData', imageData);

    imageData.$inject = ['$http', '$window'];

    function imageData($http, $window) {

      var imgStore = {};

      var communityLogo = "";

      var links = ['transperent_logo', 'apila_logo', 'apila_form'];

        $http.get('https://s3-us-west-2.amazonaws.com/apilatest2/' + links[0])
        .success(function(response) {
          imgStore[links[0]] = response;
        });

        $http.get('https://s3-us-west-2.amazonaws.com/apilatest2/' + links[1])
        .success(function(response) {
          imgStore[links[1]] = response;
        });

        $http.get('https://s3-us-west-2.amazonaws.com/apilatest2/' + links[2])
        .success(function(response) {
          imgStore[links[2]] = response;
        });


        function imageToBase64(url, callback) {

          if(!url) {
            callback(null);
          } else {
            var xhr = new XMLHttpRequest();
            xhr.onload = function() {
              var reader = new FileReader();
              reader.onloadend = function() {
                callback(reader.result);
              }
              reader.readAsDataURL(xhr.response);
            };
            xhr.open('GET', url);
            xhr.responseType = 'blob';
            xhr.send();
          }

        }

        function loadLogo(url) {
          imageToBase64(url, function(encodedImg) {
            $window.localStorage['community-logo'] = encodedImg;
          });
        }

      function changeLogo(data) {
        $window.localStorage['community-logo'] = data;
      }

      // replace the 'n'th character of 's' with 't'
      function replaceAt(s, n, t) {
          return s.substring(0, n) + t + s.substring(n + 1);
      }


      function getImage(image) {
        return replaceAt(imgStore[image], imgStore[image].length-1, '');
      }

      function getLogo() {
        return $window.localStorage['community-logo'];
      }

      return {
        getImage: getImage,
        getLogo: getLogo,
        loadLogo: loadLogo,
        changeLogo: changeLogo,
        imageToBase64: imageToBase64
      }

    }
  })();

(function() {

  angular
      .module('app.core')
      .service('errorCheck', errorCheck);

  errorCheck.$inject = [];

  function errorCheck() {

    function hasError(name, form, errors) {
      if(!form[name]) {
        errors[name] = true;
        return true;
      } else {
        errors[name] = false;
        return false;
      }
    }

    function requiredFields(form, errors, fields) {

      if(!errors) {errors = {};}

      var error = false;

      for(var i = 0; i < fields.length; ++i){
        if(hasError(fields[i], form, errors)) {
          error = true;
        }
      }

      return error;
    }

    return {
      requiredFields: requiredFields
    };
  }

})();

(function() {

    angular
        .module('app.core')
        .service('authentication', authentication);

    authentication.$inject = ['$http', '$window', '$log', 'TourService'];

    function authentication($http, $window, $log, TourService) {

       var apiUrl="";

       var changedUsername = "";
       var community = {};

       var userImage = "assets/images/avatars/userDefaultProfile.png";

        // create a saveToken method to read a value from localStorage
        var saveToken = function(token) {
            $window.sessionStorage['apila-token'] = token;
            var name = JSON.parse($window.atob(token.split('.')[1])).name;
            $window.sessionStorage['apila-username'] = $window.btoa(name);
        };

        // create a getToken method to read a value from localStorage
        var getToken = function() {
            return $window.sessionStorage['apila-token'];
        };

        var isLoggedIn = function() {
            var token = getToken();

            if (token) {
                var payload = JSON.parse($window.atob(token.split('.')[1]));

                return payload.exp > Date.now() / 1000;
            } else {
                return false;
            }
        };

        var currentUser = function() {
            if (isLoggedIn()) {
                var token = getToken();
                var payload = JSON.parse($window.atob(token.split('.')[1]));

                var encodedName = $window.sessionStorage['apila-username'];

                var name = $window.atob(encodedName);

                getCommunity(payload._id);

                return {
                    email: payload.email,
                    name: name,
                    id: payload._id,
                    community: community,
                    communityId: payload.community,
                    todoid: payload.todoid
                };
            }
        };

        var changeUsername = function(username) {
          $window.sessionStorage['apila-username'] = $window.btoa(username);

        };

        var getUserImage = function() {
          if(userImage === "") {
            return "assets/images/avatars/userDefaultProfile.png";
          } else {
            return userImage;
          }

        };

        var setUserImage = function(image) {
          userImage = image;
        };

        var register = function(user) {
            return $http.post(apiUrl + '/api/register', user).success(function(data) {
                saveToken(data.token);
            });
        };

        var login = function(user) {
            return $http.post(apiUrl + '/api/login', user).success(function(data) {
                saveToken(data.token);
                TourService.setStart(data.firstLogin);
            });
        };

        var logout = function() {
            $window.sessionStorage.removeItem('apila-token');
            $window.sessionStorage.removeItem('apila-username');
        };

        function getCommunity(id) {
          $http.get(apiUrl + '/api/users/community/' + id, {"headers": {
            Authorization: 'Bearer ' + getToken()
          }})
          .success(function(response) {
            community = response;
          })
          .error(function(response) {
            $log.debug(response);
          });
        }

        if (isLoggedIn()) {
          $http.get(apiUrl + '/api/users/' + currentUser().id + "/image")
          .success(function(response) {
            userImage = response;
          })
          .error(function(response) {
            $log.debug("Unable to load user image");
          });
        }

        // expose methods to application
        return {
            currentUser: currentUser,
            saveToken: saveToken,
            getToken: getToken,
            isLoggedIn: isLoggedIn,
            register: register,
            login: login,
            logout: logout,
            getUserImage: getUserImage,
            setUserImage: setUserImage,
            changeUsername : changeUsername,
            getCommunity : getCommunity
        };
    }


})();

(function() {

  angular
    .module('app.core')
    .service('apilaData', apilaData);

  apilaData.$inject = ['$http', 'authentication', 'IssuesApi', 'CommunitiesApi', 'ResidentsApi', 'AppointmentsApi', 'UsersApi', 'TodosApi'];

  function apilaData($http, authentication, IssuesApi, CommunitiesApi, ResidentsApi, AppointmentsApi, UsersApi, TodosApi) {

    ////////////////////////////////////////////////

    var apiUrl="";

    function getAuth() {
      return {
        "headers": {
          Authorization: 'Bearer ' + authentication.getToken()
        }
      };
    }

    IssuesApi.setApiUrl(apiUrl);
    var issuesApiRoutes = IssuesApi.getIssueRoutes();

    ResidentsApi.setApiUrl(apiUrl);
    var residentsApiRoutes = ResidentsApi.getResidentsRoutes();

    CommunitiesApi.setApiUrl(apiUrl);
    var communitiesApiRoutes = CommunitiesApi.getCommunitiesRoutes();

    AppointmentsApi.setApiUrl(apiUrl);
    var appointmentsApiRoutes = AppointmentsApi.getAppointmentsRoutes();

    UsersApi.setApiUrl(apiUrl);
    var usersApiRoutes = UsersApi.getUsersRoutes();

    TodosApi.setApiUrl(apiUrl);
    var todosApiRoutes = TodosApi.getTodosRoutes();

    var getApiUrl = function() {
      return apiUrl;
    };

    var createToDoActivity = function(todoId, data) {
      return $http.post(apiUrl + '/api/activity/' + todoId, data, getAuth());
    };

    var listLogs = function(communityid) {
      return $http.get(apiUrl + '/api/logs/' + communityid, getAuth());
    };

    var listUserLogs = function(communityid, userid) {
      return $http.get(apiUrl + '/api/user_logs/' + communityid + "/user/" + userid, getAuth());
    };

    return _.extend({

      getApiUrl: getApiUrl,

      listLogs: listLogs,
      listUserLogs: listUserLogs,

      createToDoActivity: createToDoActivity

    }, issuesApiRoutes, communitiesApiRoutes, residentsApiRoutes, appointmentsApiRoutes, usersApiRoutes, todosApiRoutes);
  }

})();

