$templateCache.put("app/main/information/information-content/calendar.html","<div class=\"demo-content\"><p>\nThe Calandar section is for displaying items in terms of time. The main item it tracks is appointments for residents.\n</p><br><h2>Creating an Appointment</h2><p>\nClick on the day of the desired appointment and a window will pop up. Fill out atleast the following fields:<ul><li>\nReason</li><li>Resident</li><li>Location</li><li>Date</li><li>Time</li><li>Transportation</li></ul>\nThen click the save button at the bottom of the window.</p><br><h2>Updating an Appointment</h2><p>\nClick on the appointment that needs to be updated. The window with all the appointments infomation will pop up. Edit any field and click save.\n</p><br><h2>Canceling an Appointment</h2><p>\nClick on the appointment that needs to be canceled. The window with all the appointments infomation will pop up. Click a checkbox at the bottom to cancel an appointment. This will cause the appointment to appear in red on the calendar.\n</p></div>");
$templateCache.put("app/main/information/information-content/dashboard.html","<div class=\"demo-content\"><p>The Dashboard is a summary and control point of the community.</p><br><h2>Dashboard Tab\n</h2><p>\nThe summary of the community with initially three displays of todays appointments, number of residents in the building, and number of open issues.\n<br>Then a table of statistics based on residents in the community that cover the following:<ul><lh>\nThen a table of statistics based on residents in the community that cover the following:</lh><li>Average Age</li><li>\nAverage Time Stayed</li></ul><br>Lastly a map showing where all the residents in the community have moved from</p><br>\n<h2>Community Members Tab</h2><p>Displays the users that are part of the community. Do all the following from this tab:\n<ul><li>Remove members from the community</li><li>Change access levels</li><li>Recover confidential issues</li></ul>\n</p><br><h2>Pending Members Tab</h2><p>Access users that are requesting to join your community.</p><br><h2>\nCommunity Settings Tab</h2><p>Change settings about the community that would include:<ul><li>Billing information</li>\n</ul></p></div>");
$templateCache.put("app/main/information/information-content/issues.html","<div class=\"demo-content\"><p>Enter infomation that needs to be tracked and resolved.</p><h2>Status</h2><p>\nIssues can be open, closed, or shelved. This will move an issue between the respective boards.</p><br><h2>Labels</h2>\n<p>\nCreate a label when an issue is created to help with organization of issues. Select a title and a color for the label.\n</p></div>");
$templateCache.put("app/main/information/information-content/residents.html","<div class=\"demo-content\"><p>\nHave the ability to enter a large amount of information about a resident so it call all be exported and tracked throughout the rest of Apila.\n</p><h2>Creating a Resident</h2><p>Click on the \"Add Resident\" button in the top left<ul><li>First Name</li><li>\nLast Name</li><li>Date of Birth</li><li>Sex</li><li>Building Status</li></ul>\nThen click the save button at the bottom of the window.</p><br><h2>Updating a Resident</h2><p>\nSelect the catagory that needs to be updated in the menu of categories to the left. Then click the update button on a resident that needs to be updated.\n</p></div>");
